  getLubricantDemandData(
    marketYearCategoryId: number,
    studyProjectMarketYearId: number,
    currencyId: number = 0,
    volumeUnitId: number = 0,
    currencyConversionType: number = 0
  ) {
    const qParams = new HttpParams()
      .set('MarketYearCategoryId', marketYearCategoryId)
      .set('StudyProjectMarketYearId', studyProjectMarketYearId)
      .set('CurrencyId', currencyId)
      .set('VolumeUnitId', volumeUnitId)
      .set('CurrencyConversionType', currencyConversionType);

    return this.http
      .get<ApiResponse>(`${this.apiEndpoint}/LubricantDemand/LubricantDemand`, {
        params: qParams,
      })
      .pipe(
        map((data) => {
          return data;
        })
      );
  }

  saveLubricantData(formData: any) {
    return this.http
      .put<ApiResponse>(
        `${this.apiEndpoint}/LubricantDemand/LubricantDemand`,
        formData,
        {
          headers: {
            'Content-Type': 'application/json',
          },
        }
      )
      .pipe(
        map((data) => {
          return data;
        })
      );
  }

