  exportToExcel(
    data: any,
    state: any,
    settings: any,
    title: string,
    userMenuSettings: any,
    viewId: number = 0
  ) {
    let url: string = this.apiEndpoint + '/PivotToExcelConvert';
    const pivotData = JSON.stringify(data);
    const userState = JSON.stringify(state);
    const userSettings = JSON.stringify(settings);
    const menuSettings = JSON.stringify(userMenuSettings);

    const headers = new HttpHeaders()
      .set('Content-Type', 'application/json')
      .set('Cache-control', 'no-store')
      .set('Cache-control', 'no-store')
      .set('Pragma', 'no-cache')
      .set('Expires', '0');

    return this.http.post(
      url,
      { pivotData, userState, userSettings, title, menuSettings, viewId },
      { headers, responseType: 'blob' }
    );
  }

  exportToCsv(data: any[], filename = 'data') {
    let columns = Object.keys(data[0]);
    columns = columns.filter((item) => item !== 'DataType');
    let csvData = this.convertToCSV(data, columns);
    let blob = new Blob(['\ufeff' + csvData], {
      type: 'text/csv;charset=utf-8;',
    });
    let dwldLink = document.createElement('a');
    let url = URL.createObjectURL(blob);
    let isSafariBrowser =
      navigator.userAgent.indexOf('Safari') != -1 &&
      navigator.userAgent.indexOf('Chrome') == -1;
    if (isSafariBrowser) {
      //if Safari open in new window to save file with random filename.
      dwldLink.setAttribute('target', '_blank');
    }
    dwldLink.setAttribute('href', url);
    dwldLink.setAttribute('download', filename + '.csv');
    dwldLink.style.visibility = 'hidden';
    document.body.appendChild(dwldLink);
    dwldLink.click();
    document.body.removeChild(dwldLink);
    this.changeExcelLoading(false);
  }

  convertToCSV(objArray: any, headerList: any) {
    let array = typeof objArray != 'object' ? JSON.parse(objArray) : objArray;
    let str = '';
    let row = '';

    for (let index in headerList) {
      row += headerList[index] + ',';
    }
    row = row.slice(0, -1);
    str += row + '\r\n';
    for (let i = 0; i < array.length; i++) {
      let line = '';
      for (let index in headerList) {
        let head = headerList[index];
        let val: string = String(array[i][head]);
        if (val.includes(',')) {
          line += ',"' + array[i][head] + '"';
        } else {
          line += ',' + array[i][head];
        }
      }
      str += line.substring(1) + '\r\n';
    }
    return str;
  }

