import {
  Component,
  Input,
  OnChanges,
  OnInit,
  SimpleChanges,
} from '@angular/core';
import PivotGridDataSource, {
  Field,
} from 'devextreme/ui/pivot_grid/data_source';
import { SpecialtyExcipientItem } from 'src/model/avgPriceSE.model';

@Component({
  selector: 'app-average-price-preview',
  templateUrl: './average-price-preview.component.html',
  styleUrls: ['./average-price-preview.component.css'],
})
export class AveragePricePreviewComponent implements OnInit {
  @Input() studyData: SpecialtyExcipientItem[] = [];
  @Input() currentYear: number = 0;
  @Input() previousYear: number = 0;

  pivotGridDataSource: PivotGridDataSource;
  gridFields: Field[] = [];

  constructor() {
    this.pivotGridDataSource = new PivotGridDataSource();
  }

  ngOnInit(): void {}

  ngOnChanges(changes: SimpleChanges): void {
    this.getPreviewData(this.studyData);
  }

  getPreviewData(studyData: SpecialtyExcipientItem[]) {
    const previewData: {
      ExcipientCategory: string;
      ExcipientGroup: string;
      Excipient: string;
      Year: number;
      AvgMinPrice: number;
      AvgMaxPrice: number;
    }[] = [];

    studyData.map((item: SpecialtyExcipientItem) => {
      previewData.push({
        ExcipientCategory: item.ExcipientCategoryName,
        ExcipientGroup: item.ExcipientGroupName,
        Excipient: item.ExcipientName,
        Year: this.previousYear,
        AvgMinPrice: item.AveragePriceMin1,
        AvgMaxPrice: item.AveragePriceMax1,
      });
      previewData.push({
        ExcipientCategory: item.ExcipientCategoryName,
        ExcipientGroup: item.ExcipientGroupName,
        Excipient: item.ExcipientName,
        Year: this.currentYear,
        AvgMinPrice: item.AveragePriceMin2,
        AvgMaxPrice: item.AveragePriceMax2,
      });
    });

    this.pivotGridDataSource = new PivotGridDataSource({
      store: previewData,
      fields: this.getGridFields(),
    });
  }

  getGridFields() {
    return (this.gridFields = [
      {
        dataField: 'ExcipientCategory',
        area: 'row',
        caption: 'Excipient Category',
        dataType: 'string',
      },
      {
        dataField: 'ExcipientGroup',
        area: 'row',
        caption: 'Excipient Group',
        dataType: 'string',
      },
      {
        dataField: 'Excipient',
        area: 'row',
        caption: 'Excipient',
        dataType: 'string',
      },
      {
        visible: true,
        isCalculated: false,
        calculatedFieldType: 'none',
        id: 6,
        caption: 'Avg. Min Price',
        dataField: 'AvgMinPrice',
        dataFieldType: 'AvgMinPrice',
        width: 80,
        dataType: 'number',
        summaryType: 'sum',
        summaryDisplayMode: 'none',
        format: {
          type: 'fixedPoint',
          precision: 1,
        },
        area: 'data',
      },
      {
        visible: true,
        isCalculated: false,
        calculatedFieldType: 'none',
        id: 6,
        caption: 'Avg. Max Price',
        dataField: 'AvgMaxPrice',
        dataFieldType: 'AvgMaxPrice',
        width: 80,
        dataType: 'number',
        summaryType: 'sum',
        summaryDisplayMode: 'none',
        format: {
          type: 'fixedPoint',
          precision: 1,
        },
        area: 'data',
      },
      {
        visible: true,
        isCalculated: false,
        calculatedFieldType: 'none',
        id: 7,
        caption: 'Year',
        dataField: 'Year',
        width: 30,
        dataType: 'string',
        summaryDisplayMode: 'none',
        area: 'column',
      },
    ] as Field[]);
  }
}
