  saveStudyData() {
    const studyData = this.studyData.map((item: any) => ({
      ...item,
      PreviousYear1: item.PreviousYear1 > 0 ? item.PreviousYear1 : 0,
      PreviousYear2: item.PreviousYear2 > 0 ? item.PreviousYear2 : 0,
      PreviousYear3: item.PreviousYear3 > 0 ? item.PreviousYear3 : 0,
      PreviousYear4: item.PreviousYear4 > 0 ? item.PreviousYear4 : 0,
      PreviousYear5: item.PreviousYear5 > 0 ? item.PreviousYear5 : 0,
      CurrentYear: item.CurrentYear > 0 ? item.CurrentYear : 0,
      Forecast1: item.Forecast1 > 0 ? item.Forecast1 : 0,
      Forecast2: item.Forecast2 > 0 ? item.Forecast2 : 0,
      Forecast3: item.Forecast3 > 0 ? item.Forecast3 : 0,
      Forecast4: item.Forecast4 > 0 ? item.Forecast4 : 0,
      Forecast5: item.Forecast5 > 0 ? item.Forecast5 : 0,
    }));

    const studyDataInfo = {
      marketYearCategoryId: this.marketYearCategoryId,
      initialSetup: this.studySummary,
      productTypeSales: studyData,
    };

    this.isLoadingService.add();
    this.studyService.savePSCData('ProductTypeSales', studyDataInfo).subscribe(
      (res: ApiResponse) => {
        if (res.HttpStatusCode === 200) {
          this.alertService.success(res.Data);
          // this.dataGrid?.instance.refresh();
          this.isLoadingService.remove();
          // this.getProductTypeSalesData();
          this.loadCurrentStudy();
        } else {
          this.alertService.danger(res.Data);
          this.isLoadingService.remove();
        }
      },
      (err) => {
        console.log(err);
        this.alertService.danger(
          'Something went wrong. Please try after sometimes.'
        );
        this.isLoadingService.remove();
      }
    );
  }

