  createNewDataEntry(type: string, newValue: string) {
    const dataRelationData = this.datRelationsData.find(
      (item) =>
        (<any>Object).values(DataRelationsEnum).includes(item.TableName) &&
        item.TableName === type
    );

    if (dataRelationData) {
      this.isLoadingService.add();
      this.sharedService
        .saveNewDataEntry(dataRelationData.DataRelationId, newValue)
        .subscribe(
          (res: ApiResponse) => {
            if (res.HttpStatusCode === 200) {
              this.alertService.success(res.Data);
              this.loadDataEntry(type);
              this.isLoadingService.remove();
            } else {
              this.alertService.danger(res.Data);
              this.isLoadingService.remove();
            }
          },
          (err) => {
            console.log(err);
            this.isLoadingService.remove();
          }
        );
    }
  }

  loadDataEntry(type: string) {
    switch (type) {
      case DataRelationsEnum.Channel:
        this.getChannelLists();
        break;

      case DataRelationsEnum.ProductType:
        this.getProductTypeLists();
        break;

      default:
        break;
    }
  }

