import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AuthGuard } from 'src/guard/auth.guard';
import { DataGridsConfigurationComponent } from './data-grids-configuration/data-grids-configuration.component';
import { InitialSetupComponent } from './initial-setup/initial-setup.component';
import { LoginComponent } from './login/login.component';

const routes: Routes = [
  {
    path: 'initial-setup',
    component: InitialSetupComponent,
    canActivate: [AuthGuard],
    data: {
      role: 'MANAGER',
    },
  },
  {
    path: 'app-data-grids-configuration',
    component: DataGridsConfigurationComponent,
    canActivate: [AuthGuard],
    data: {
      role: 'MANAGER',
    },
  },
  // {
  //   path: 'study-summary',
  //   component: StudyAreaSummaryComponent,
  //   canActivate: [AuthGuard],
  // },
  {
    path: 'login',
    component: LoginComponent,
  },
  {
    path: '',
    redirectTo: 'study-summary',
    pathMatch: 'full',
  },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
})
export class AppRoutingModule {}
