import {
  Component,
  Input,
  OnChanges,
  OnInit,
  SimpleChanges,
} from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import PivotGridDataSource, {
  Field,
} from 'devextreme/ui/pivot_grid/data_source';
import { VehicleParcStatisticItem } from 'src/model/vehicleParcStatistics.model';

@Component({
  selector: 'app-vehicle-parc-statistics-preview',
  templateUrl: './vehicle-parc-statistics-preview.component.html',
  styleUrls: ['./vehicle-parc-statistics-preview.component.css'],
})
export class VehicleParcStatisticsPreviewComponent implements OnInit {
  @Input() studyData: VehicleParcStatisticItem[] = [];
  @Input() currentYear: number = 0;
  @Input() previousYear: number = 0;

  marketYearCategoryId: number = 0;
  studyProjectMarketYearId: number = 0;
  studyAreaId: number = 0;

  pivotGridDataSource: PivotGridDataSource;
  gridFields: Field[] = [];

  constructor(private route: ActivatedRoute) {
    const params = this.route.snapshot.params;

    this.marketYearCategoryId = params['mycId'];
    this.studyProjectMarketYearId = params['spmId'];
    this.studyAreaId = params['studyArea'];

    this.pivotGridDataSource = new PivotGridDataSource();
  }

  ngOnInit(): void {}

  ngOnChanges(changes: SimpleChanges): void {
    this.getPreviewData(this.studyData);
  }

  getPreviewData(studyData: VehicleParcStatisticItem[]) {
    // console.log(studyData);
    const { totalCurrentYear, totalPreviousYear1 } = studyData.reduce(
      ({ totalCurrentYear, totalPreviousYear1 }, currentYear) => ({
        totalCurrentYear: totalCurrentYear + currentYear.CurrentYear,
        totalPreviousYear1: totalPreviousYear1 + currentYear.PreviousYear1,
      }),
      { totalCurrentYear: 0, totalPreviousYear1: 0 }
    );

    const previewData: {
      VehicleType: string;
      Year: number;
      Volume: number;
      perOfTotal: number;
    }[] = [];

    studyData.map((item: VehicleParcStatisticItem) => {
      let prevPer: number = 0;
      if (item.PreviousYear1 > 0) {
        prevPer = this.calculatePerVolume(
          item.PreviousYear1,
          totalPreviousYear1
        );
      }

      let currPer: number = 0;
      if (item.CurrentYear > 0) {
        currPer = this.calculatePerVolume(item.CurrentYear, totalCurrentYear);
      }

      previewData.push({
        VehicleType: item.VehicleType,
        Year: this.previousYear,
        Volume: item.PreviousYear1,
        perOfTotal: parseFloat(prevPer.toFixed(1)),
      });
      previewData.push({
        VehicleType: item.VehicleType,
        Year: this.currentYear,
        Volume: item.CurrentYear,
        perOfTotal: parseFloat(currPer.toFixed(1)),
      });
    });

    this.pivotGridDataSource = new PivotGridDataSource({
      store: previewData,
      fields: this.getGridFields(),
    });
  }

  getGridFields() {
    return (this.gridFields = [
      {
        dataField: 'VehicleType',
        area: 'row',
        caption: 'Vehicle Type',

        dataType: 'string',
      },
      {
        visible: true,
        isCalculated: false,
        calculatedFieldType: 'none',
        id: 6,
        caption: 'Mil Unit',
        dataField: 'Volume',
        dataFieldType: 'Volume',
        width: 80,
        dataType: 'number',
        summaryType: 'sum',
        summaryDisplayMode: 'none',
        format: {
          type: 'fixedPoint',
          precision: 1,
        },
        area: 'data',
      },
      {
        visible: true,
        isCalculated: false,
        calculatedFieldType: 'none',
        id: 6,
        caption: '% of Total',
        dataField: 'perOfTotal',
        dataFieldType: 'perOfTotal',
        width: 80,
        dataType: 'number',
        summaryType: 'sum',
        summaryDisplayMode: 'none',
        format: {
          type: 'fixedPoint',
          precision: 1,
        },
        area: 'data',
      },
      {
        visible: true,
        isCalculated: false,
        calculatedFieldType: 'none',
        id: 7,
        caption: 'Year',
        dataField: 'Year',
        width: 30,
        dataType: 'string',
        summaryDisplayMode: 'none',
        area: 'column',
      },
    ] as Field[]);
  }

  calculatePerVolume(colValue: number, totalColValue: number) {
    return parseFloat(((colValue * 100) / totalColValue).toFixed(1));
  }

  changeStudyData(studyData: VehicleParcStatisticItem[]) {
    this.studyData = studyData;
    this.getPreviewData(this.studyData);
  }
}
