import {
  Component,
  Input,
  OnChanges,
  OnInit,
  SimpleChanges,
} from '@angular/core';
import PivotGridDataSource, {
  Field,
} from 'devextreme/ui/pivot_grid/data_source';
import { SpecialtyExcipientSaleItem } from 'src/model/specialtyExcipientSales.model';

@Component({
  selector: 'app-specialty-excipient-sales-preview',
  templateUrl: './specialty-excipient-sales-preview.component.html',
  styleUrls: ['./specialty-excipient-sales-preview.component.css'],
})
export class SpecialtyExcipientSalesPreviewComponent implements OnInit {
  @Input() studyData: SpecialtyExcipientSaleItem[] = [];
  @Input() currentYear: number = 0;
  @Input() previousYear: number = 0;

  pivotGridDataSource: PivotGridDataSource;
  gridFields: Field[] = [];

  constructor() {
    this.pivotGridDataSource = new PivotGridDataSource();
  }

  ngOnInit(): void {}

  ngOnChanges(changes: SimpleChanges): void {
    this.getPreviewData(this.studyData);
  }

  getPreviewData(studyData: SpecialtyExcipientSaleItem[]) {
    const previewData: {
      ExcipientCategory: string;
      ExcipientGroup: string;
      Excipient: string;
      Supplier: string;
      Year: number;
      Sales: number;
      Volume: number;
      CAGR: number;
    }[] = [];

    studyData.map((item: SpecialtyExcipientSaleItem) => {
      previewData.push({
        ExcipientCategory: item.ExcipientCategoryName,
        ExcipientGroup: item.ExcipientGroupName,
        Excipient: item.ExcipientName,
        Supplier: item.SupplierName,
        Year: this.previousYear,
        Sales: item.Value1,
        Volume: item.Volume1,
        CAGR: item.CAGR1,
      });
      previewData.push({
        ExcipientCategory: item.ExcipientCategoryName,
        ExcipientGroup: item.ExcipientGroupName,
        Excipient: item.ExcipientName,
        Supplier: item.SupplierName,
        Year: this.currentYear,
        Sales: item.Value2,
        Volume: item.Volume2,
        CAGR: item.CAGR2,
      });
    });

    this.pivotGridDataSource = new PivotGridDataSource({
      store: previewData,
      fields: this.getGridFields(),
    });
  }

  getGridFields() {
    return (this.gridFields = [
      {
        dataField: 'ExcipientCategory',
        area: 'row',
        caption: 'Excipient Category',
        dataType: 'string',
      },
      {
        dataField: 'ExcipientGroup',
        area: 'row',
        caption: 'Excipient Group',
        dataType: 'string',
      },
      {
        dataField: 'Excipient',
        area: 'row',
        caption: 'Excipient',
        dataType: 'string',
      },
      {
        dataField: 'Supplier',
        area: 'row',
        caption: 'Supplier',
        dataType: 'string',
      },
      {
        visible: true,
        isCalculated: false,
        calculatedFieldType: 'none',
        id: 6,
        caption: 'Sales',
        dataField: 'Sales',
        dataFieldType: 'Sales',
        width: 80,
        dataType: 'number',
        summaryType: 'sum',
        summaryDisplayMode: 'none',
        format: {
          type: 'fixedPoint',
          precision: 1,
        },
        area: 'data',
      },
      {
        visible: true,
        isCalculated: false,
        calculatedFieldType: 'none',
        id: 6,
        caption: 'Volume',
        dataField: 'Volume',
        dataFieldType: 'Volume',
        width: 80,
        dataType: 'number',
        summaryType: 'sum',
        summaryDisplayMode: 'none',
        format: {
          type: 'fixedPoint',
          precision: 1,
        },
        area: 'data',
      },
      {
        visible: true,
        isCalculated: false,
        calculatedFieldType: 'none',
        id: 6,
        caption: 'CAGR(%)',
        dataField: 'CAGR',
        dataFieldType: 'CAGR',
        width: 80,
        dataType: 'number',
        summaryType: 'sum',
        summaryDisplayMode: 'none',
        format: {
          type: 'fixedPoint',
          precision: 1,
        },
        area: 'data',
      },
      {
        visible: true,
        isCalculated: false,
        calculatedFieldType: 'none',
        id: 7,
        caption: 'Year',
        dataField: 'Year',
        width: 30,
        dataType: 'string',
        summaryDisplayMode: 'none',
        area: 'column',
      },
    ] as Field[]);
  }
}
