import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { HotTableRegisterer } from '@handsontable/angular';
import Handsontable from 'handsontable';
import { HyperFormula } from 'hyperformula';
@Component({
  selector: 'app-handsontable',
  templateUrl: './handsontable.component.html',
  styleUrls: ['./handsontable.component.css'],
})
export class HandsontableComponent implements OnInit {
  data1 = [
    ['10.26', null, 'Sum', '=SUM(A:A)'],
    ['20.12', null, 'Average', '=AVERAGE(A:A)'],
    ['30.01', null, 'Median', '=MEDIAN(A:A)'],
    ['40.29', null, 'MAX', '=MAX(A:A)'],
    ['50.18', null, 'MIN', '=MIN(A1:A5)'],
  ];
  hyperformulaInstance = HyperFormula.buildEmpty({
    // to use an external HyperFormula instance,
    // initialize it with the `'internal-use-in-handsontable'` license key
    licenseKey: 'internal-use-in-handsontable',
  });

  // @Input() manualColumnMove: any
  @Input() colHeaders: any;
  @Input() settings: any;
  @Input() columns: any;
  @Input() data: any;
  @Input() cell: any;
  @Input() nestedHeaders: any;
  @Input() fixedRowsTop: number = 2;
  @Input() fixedColumnsLeft: number = 2;
  @Input() formulas: any;
  @Output() afterOnCellMouseDownEvent = new EventEmitter<string>();
  private hotRegisterer = new HotTableRegisterer();
  constructor() {}

  id = '1';
  ngOnInit(): void {}

  afterFormulasValuesUpdate = (changes: any) => {
    console.log(changes);
  };
  test() {
    console.log('test:');
    console.log(event);
    console.log(this.hotRegisterer.getInstance(this.id));
  }
  afterOnCellMouseDown(event: any) {
    this.afterOnCellMouseDownEvent.emit(event);
  }
}
