import { SEDataEntry } from './dataEntrySE.model';
import { ComputingType } from './enums/computingType.enum';
import { CurrencyConversionTypeEnum } from './enums/currencyConversionType.enum';
// import { UnitDropdown } from "./unitDropdown.model";
// import { ComputingType } from "../enums/computingType.enum";
import { PivotGridDataFieldType } from './enums/pivotGridDataFieldType.enum';
// import { BasePeriodDropdown } from './basePeriodDropdown.model';

export class UserStateAndSetttings {
  settings: UserSettings = new UserSettings();
  state: any;
}

export class UserChartSetting {
  dataFieldsDisplayMode: string = 'singleAxis';
  chartType: string = 'Column';
}

export class UserSettings {
  currency: CurrencyDropdown | undefined;
  currencyConversionType: CurrencyConversionTypeEnum | undefined = undefined;
  unitOfMass: UnitDropdown | undefined;
  unitOfMeasure: UnitDropdown | undefined;
  unitOfVolume: UnitDropdown | undefined;
  selectedYear: any = undefined;
  percentOfTotalState: PercentOfTotalState = new PercentOfTotalState();
  changePercentState: ChangePercentState = new ChangePercentState();
  cagrState: CagrState = new CagrState();
  dataFieldArea: string = 'column';
  userChartSetting: UserChartSetting = new UserChartSetting();
  latestAcessibleYear: any;
  isReducedDataSource = false;
  rememberLatestState: any = undefined;
}

export class CurrencyDropdown {
  Id: number = 0;
  Code: string = '';
  Name: string = '';
}

export class UnitDropdown {
  Id: number;
  Name: string;
}

export class PercentOfTotalState {
  isSet: boolean = false;
  dataField: PivotGridDataFieldType | undefined;
  basedOn: string = 'Subtotals';
  summaryDisplayMode: string = 'percentOfColumnTotal';
}

export class ChangePercentState {
  isSet: boolean = false;
  dataField: PivotGridDataFieldType | undefined;
  computingType: string = ComputingType.FirstYear;
}

export class CagrState {
  isSet: boolean = false;
  dataField: PivotGridDataFieldType | undefined;
  computingType: string = ComputingType.FirstYear;
}
