import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AlertService } from '@full-fledged/alerts';
import { IsLoadingService } from '@service-work/is-loading';
import { ApiResponse } from 'src/model/apiResponse.model';
import { InitialSetup } from 'src/model/initialSetup.model';
import {
  SpecialtyExcipientSaleItem,
  SpecialtyExcipientSales,
} from 'src/model/specialtyExcipientSales.model';
import { StudyService } from 'src/services/study.service';

@Component({
  selector: 'app-calculator',
  templateUrl: './calculator.component.html',
  styleUrls: ['./calculator.component.css'],
})
export class CalculatorComponent implements OnInit {
  calculatorId: number = 0;
  marketYearCategoryId: number = 0;
  StudyProjectMarketYearId: number = 0;
  calculatedData: any;
  calculatorOutputTemplateData: any;
  restartInputComponent: boolean = false;

  studyInitialSetup: InitialSetup = new InitialSetup().deserialize({});

  studySpecialtyExcipientData: any = null;

  Currency: any = {};
  Volume: any = {};

  constructor(
    private activeRoute: ActivatedRoute,
    private route: Router,
    private studyService: StudyService,
    private alertService: AlertService,
    private isLoadingService: IsLoadingService
  ) {
    const params = this.activeRoute.snapshot.params;
    this.calculatorId =
      typeof params['calculatorId'] !== 'undefined'
        ? params['calculatorId']
        : 0;

    this.marketYearCategoryId =
      typeof params['marketYearCategoryId'] !== 'undefined'
        ? params['marketYearCategoryId']
        : 0;

    this.StudyProjectMarketYearId =
      typeof params['studyProjectMarketYearId'] !== 'undefined'
        ? params['studyProjectMarketYearId']
        : 0;
  }

  ngOnInit(): void {
    this.getSalesData();
  }

  startOver() {
    this.restartInputComponent = true;
    this.calculatedData = undefined;
    // prevent double click call for input data
    setTimeout(() => {
      this.restartInputComponent = false;
    }, 1000);
  }

  onComputed(calculatedData: any) {
    this.calculatedData = calculatedData;
  }

  onCalculatorTemplateReturned(calculatorOutputTemplateData: any) {
    this.calculatorOutputTemplateData = calculatorOutputTemplateData;
    // this.industryId = calculatorOutputTemplateData.IndustryId;
  }

  getSalesData() {
    this.studyService
      .getSalesData(this.marketYearCategoryId, this.StudyProjectMarketYearId)
      .subscribe((res: ApiResponse) => {
        if (res.HttpStatusCode === 200) {
          // console.log(res.Data);
          const studyData = res.Data as SpecialtyExcipientSales;

          this.studySpecialtyExcipientData = studyData;

          this.studyInitialSetup = new InitialSetup().deserialize(
            studyData.InitialSetup
          );

          studyData.SpecialtyExcipientsSales.map(
            (singleItem: SpecialtyExcipientSaleItem) => {
              this.Currency = {
                Name: singleItem.CurrencyName,
                Code: singleItem.CurrencyCode,
                Id: singleItem.CurrencyId,
              };

              this.Volume = {
                Name: singleItem.VolumeUnitName,
                Code: singleItem.VolumeUnitId,
              };
            }
          );
        }
      });
  }

  onExportSpecialtyExcipientData(specialtyExcipientData: any) {
    const studyDataInfo = {
      marketYearCategoryId: this.marketYearCategoryId,
      initialSetup: this.studyInitialSetup,
      specialtyExcipientsSales: specialtyExcipientData,
    };

    this.isLoadingService.add();
    this.studyService.saveSESData(studyDataInfo).subscribe(
      (res: ApiResponse) => {
        if (res.HttpStatusCode === 200) {
          this.alertService.success(res.Data);
          // this.dataGrid?.instance.refresh();
          // this.reloadCurrentRoute();
          // this.isLoadingService.remove();
          this.route.navigateByUrl(this.getStudyUrl());
        } else {
          this.alertService.danger(res.Data);
          this.isLoadingService.remove();
        }
      },
      (err) => {
        const msg =
          typeof err.message !== 'undefined'
            ? err.message
            : 'Something went wrong.';
        this.alertService.danger(msg);
        this.isLoadingService.remove();
      }
    );
  }

  getStudyUrl() {
    const url = `/study-area/${this.studyInitialSetup.StudyAreaId}/${this.StudyProjectMarketYearId}/${this.marketYearCategoryId}/${this.studyInitialSetup.StudyDataSetId}`;
    return url;
  }
}
