import { outputAst } from '@angular/compiler';
import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

import { PendingChangesService } from 'src/services/pending-changes.service';
import { HandsonTableComponent } from '../handson-table/handson-table.component';

@Component({
  selector: 'app-confirm-navigation',
  templateUrl: './confirm-navigation.component.html',
  styleUrls: ['./confirm-navigation.component.css']
})
export class ConfirmNavigationComponent {
  @Output() saveChanges = new EventEmitter<any>();
  constructor(private pendingChangesService: PendingChangesService){}
  @Input() opened:any;

  confirm(): void {
    this.pendingChangesService.confirm();
  }

  cancel(): void {
    this.pendingChangesService.setPendingChanges(false);
    this.saveChanges.emit();
    this.pendingChangesService.cancel();
  }
  public onDialogClose() {
    this.opened = false;
    this.pendingChangesService.cancel();
  }

}
