import { Component, OnInit, ViewChild } from '@angular/core';
import {
  AbstractControl,
  FormBuilder,
  FormControl,
  FormGroup,
  Validators,
} from '@angular/forms';
import { AlertService } from '@full-fledged/alerts';
import { IsLoadingService } from '@service-work/is-loading';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { Analyst } from 'src/model/analyst.model';
import { ApiResponse } from 'src/model/apiResponse.model';
import { InitialSetup } from 'src/model/initialSetup.model';
import { Market } from 'src/model/market.model';
import { Project } from 'src/model/project.model';
import { ProjectLead } from 'src/model/projectLead.model';
import { StudyAreaCategory, StudyDataSet } from 'src/model/studyDataSet.model';
import { UserDetail } from 'src/model/userDetail.model';
import { LocalStorageService } from 'src/services/localStorage.service';
import { SharedService } from 'src/services/shared.service';
import { Constants } from '../Constant';

@Component({
  selector: 'app-initial-setup',
  templateUrl: './initial-setup.component.html',
  styleUrls: ['./initial-setup.component.css'],
})
export class InitialSetupComponent implements OnInit {
  studyAreas: StudyDataSet[] = [];
  studyAreaCategories: StudyAreaCategory[] = [];
  projects: Project[] = [];
  studyYears: number[] = [];
  projectLeads: ProjectLead[] = [];
  markets: Market[] = [];
  analyst: Analyst[] = [];
  preFillFormData: InitialSetup[] = [];

  initialSetupForm: FormGroup;

  initialSetupData: any = null;

  submitted: boolean = false;

  showSuccessMsg = false;

  studySummary: any;

  @ViewChild('prefillFromModel', { static: false })
  prefillFromModel?: ModalDirective;

  studyAreaSummary: any[] = [];
  userDetail: UserDetail;

  constructor(
    private sharedService: SharedService,
    private formBuilder: FormBuilder,
    private alertService: AlertService,
    private isLoadingService: IsLoadingService,
    private localStorageService: LocalStorageService
  ) {
    if (this.isLoadingService.isLoading$()) {
      this.isLoadingService.remove();
    }
    const currentYear = new Date().getFullYear();
    // for (var i = currentYear + 1; i >= currentYear - 10; i--) {
    //   this.studyYears.push(i);
    // }

    this.userDetail = this.localStorageService.getData(Constants.userDetailKey);

    // this.initialSetupForm = this.initialSetupForm.patchValue({
    this.initialSetupForm = this.formBuilder.group({
      studyDataSetId: [null, Validators.required],
      studyProjectId: [null, Validators.required],
      projectLeadId: [null, Validators.required],
      analystId: [null, Validators.required],
      studyAreaId: [null],
      studyYear: [null, Validators.required],
      marketId: [null, Validators.required],
      name: ['', Validators.required],
      description: ['', Validators.required],
      preFillForm: [null],
    });
  }

  ngOnInit(): void {
    this.getStudyArea();
    this.getProjectLeads();
    this.getAnalyst();
  }

  get f(): { [key: string]: AbstractControl } {
    return this.initialSetupForm.controls;
  }

  getStudyArea() {
    this.sharedService.getStudyArea().subscribe((res: ApiResponse) => {
      if (res.HttpStatusCode === 200) {
        this.studyAreas = res.Data as StudyDataSet[];
        // this.getProjects();
      }
    });
  }

  changeDataSet() {
    const dataSet = this.initialSetupForm.get('studyDataSetId')?.value;
    let studyYear = this.initialSetupForm.get('studyYear')?.value;
    this.studyYears = [];
    if (dataSet !== null) {
      studyYear = studyYear > 0 ? studyYear : 0;
      dataSet.Years.map((year: number) => {
        this.studyYears.push(year);
      });

      // console.log(this.studyYears);
      // this.getProjects(dataSet.Id, studyYear);
      this.getStudyAreaCategory(dataSet.Id);
      // this.getMarket(dataSet.Id, studyYear);
    } else {
      this.studyAreaCategories = [];
    }
    this.initialSetupForm.get('studyYear')?.setValue(null);
    this.initialSetupForm.get('studyProjectId')?.setValue(null);
    this.initialSetupForm.get('studyAreaId')?.setValue(null);
    this.initialSetupForm.get('marketId')?.setValue(null);
    this.initialSetupForm.updateValueAndValidity();

    this.generateStudyName();
  }

  changeStudyYear() {
    const dataSet = this.initialSetupForm.get('studyDataSetId')?.value;
    let studyYear = this.initialSetupForm.get('studyYear')?.value;
    if (dataSet !== null) {
      studyYear = studyYear > 0 ? studyYear : 0;
      this.getProjects(dataSet.Id, studyYear);
      this.getMarket(dataSet.Id, studyYear);
    }
    this.initialSetupForm.get('studyProjectId')?.setValue(null);
    this.initialSetupForm.get('marketId')?.setValue(null);
    this.initialSetupForm.updateValueAndValidity();

    this.generateStudyName();
  }

  getStudyAreaCategory(dataSetId: number) {
    this.sharedService
      .getStudyAreaCategory(dataSetId)
      .subscribe((res: ApiResponse) => {
        if (res.HttpStatusCode === 200) {
          this.studyAreaCategories = res.Data as StudyAreaCategory[];
          if(dataSetId == 4){
            const lastIndex = this.studyAreaCategories.length - 1;
            if(lastIndex>0){
              this.studyAreaCategories[lastIndex-1] = this.studyAreaCategories.splice(lastIndex,1,this.studyAreaCategories[lastIndex-1])[0];
            }
          }
          // this.getProjects(dataSetId);
        }
      });
  }

  getProjects(dataSetId: number, year: number) {
    this.sharedService
      .getProject(dataSetId, year)
      .subscribe((res: ApiResponse) => {
        if (res.HttpStatusCode === 200) {
          this.projects = res.Data as Project[];
          const firstProject =
            typeof this.projects[0] !== 'undefined' ? this.projects[0] : {};
          if (firstProject) {
            this.initialSetupForm.get('studyProjectId')?.setValue(firstProject);
          }
        }
      });
  }

  getProjectLeads() {
    this.sharedService.getProjectLead().subscribe((res: ApiResponse) => {
      if (res.HttpStatusCode === 200) {
        this.projectLeads = res.Data as ProjectLead[];
        this.initialSetupForm
          .get('projectLeadId')
          ?.setValue(this.userDetail.Id);
      }
    });
  }

  getMarket(dataSetId: number, year: number) {
    this.sharedService
      .getMarket(dataSetId, year)
      .subscribe((res: ApiResponse) => {
        if (res.HttpStatusCode === 200) {
          this.markets = res.Data as Market[];
        }
      });
  }

  getAnalyst() {
    this.sharedService.getAnalyst().subscribe((res: ApiResponse) => {
      if (res.HttpStatusCode === 200) {
        this.analyst = res.Data as Analyst[];
      }
    });
  }

  getInitialSetupByMarketId(marketId: number, studyArea: number) {
    this.sharedService
      .getInitialSetupByMarketId(marketId, studyArea)
      .subscribe((res: ApiResponse) => {
        if (res.HttpStatusCode === 200) {
          this.preFillFormData = res.Data.map((list: InitialSetup) =>
            new InitialSetup().deserialize(list)
          );
        }
      });
  }

  onMarketChange() {
    const selectedMarket = this.initialSetupForm.get('marketId')
      ?.value as Market;

    const selectedStudyArea = this.initialSetupForm.get('studyAreaId')?.value;
    this.initialSetupForm.get('preFillForm')?.setValue(null);
    if (selectedMarket && selectedStudyArea) {
      this.getInitialSetupByMarketId(selectedMarket.Id, selectedStudyArea.Id);
    }
    this.generateStudyName();
  }

  onStudyAreaChange() {
    const selectedStudyArea = this.initialSetupForm.get('studyAreaId')?.value;
    if (
      selectedStudyArea &&
      selectedStudyArea.Id === 26 &&
      selectedStudyArea.DataSetStudyId === 3
    ) {
      this.initialSetupForm.get('marketId')?.setValue(null);
      this.initialSetupForm.get('marketId')?.disable();
    } else {
      this.initialSetupForm.get('marketId')?.enable();
    }

    const selectedMarket = this.initialSetupForm.get('marketId')
      ?.value as Market;
    this.initialSetupForm.get('preFillForm')?.setValue(null);
    if (selectedMarket && selectedStudyArea) {
      this.getInitialSetupByMarketId(selectedMarket.Id, selectedStudyArea.Id);
    }
    this.generateStudyName();
  }

  onMarketClear() {
    this.initialSetupForm.get('marketId')?.enable();
    this.preFillFormData = [];
    this.initialSetupForm.get('preFillForm')?.setValue(null);
  }

  generateStudyName() {
    const studyDataSet = this.initialSetupForm.get('studyDataSetId')?.value;
    const studyDataYear = this.initialSetupForm.get('studyYear')?.value;
    const studyDataMarket = this.initialSetupForm.get('marketId')?.value;
    const studyArea = this.initialSetupForm.get('studyAreaId')?.value;
    const studyAreaId = studyArea !== null ? studyArea.Description : '';
    const givenRef = this.convertDateToHexCode();

    //Abbrivating DataSet Name 
    var originalDataSetName = studyDataSet ? studyDataSet.Name : '';
    var matches = originalDataSetName.match(/\b(\w)/g);
    const studyDataSetName = matches.join('');
    
    const studyDataMarketName = studyDataMarket
      ? studyDataMarket.ShortName.replace('All Other','AO')
      : '';

    let studyName = '';
    if (
      studyDataSetName &&
      studyDataYear &&
      studyDataMarketName &&
      studyAreaId
    ) {
      studyName = `${studyDataSetName}-${studyDataYear}-${studyDataMarketName}-${studyAreaId}-${givenRef}`;
    } else if (studyDataSetName && studyDataMarketName && studyAreaId) {
      studyName = `${studyDataSetName}-${studyDataMarketName}-${studyAreaId}-${givenRef}`;
    } else if (studyDataSetName && studyDataYear && studyAreaId) {
      studyName = `${studyDataSetName}-${studyDataYear}-${studyAreaId}-${givenRef}`;
    } else if (studyDataSetName && studyAreaId) {
      studyName = `${studyDataSetName}-${studyAreaId}-${givenRef}`;
    } else if (studyAreaId) {
      studyName = `${studyAreaId}-${givenRef}`;
    }

    this.initialSetupForm.get('name')?.setValue(studyName);
  }

  convertDateToHexCode(){
    const date = new Date();
    const year = date.getFullYear();
    const mm = date.getMonth()+1;
    const day = date.getDate();
    const hh = date.getHours();
    const min = date.getMinutes();
    const sec = date.getSeconds();
    const dateString = parseInt(""+mm+day+hh+min+sec);
    return (dateString.toString(16));
  }
  getStudyAreaSummaryByMarketYearId() {
    const preFillForm: InitialSetup =
      this.initialSetupForm.get('preFillForm')?.value;

    this.isLoadingService.add();
    this.sharedService
      .getStudyAreaSummaryByStudyMarketYearId(preFillForm.Id)
      .subscribe((res: ApiResponse) => {
        if (res.HttpStatusCode === 200) {
          this.studyAreaSummary = res.Data;

          const selectedStudyArea =
            this.initialSetupForm.get('studyAreaId')?.value;
          this.sharedService
            .getInitialSetupById(preFillForm.Id, selectedStudyArea.Id)
            .subscribe((response: ApiResponse) => {
              if (response.HttpStatusCode === 200) {
                const initialSetupData = response.Data.map(
                  (item: InitialSetup) => new InitialSetup().deserialize(item)
                );
                this.initialSetupData = initialSetupData[0] as InitialSetup;
                this.isLoadingService.remove();
                this.prefillFromModel?.show();
              }
            });
        }
      });
  }

  onFromSubmit() {
    this.submitted = true;
    if (this.initialSetupForm.invalid) {
      return;
    }

    const projectLead = this.projectLeads.find(
      (item) => item.Id === this.initialSetupForm.value.projectLeadId
    );

    const studyData = this.initialSetupForm.value.studyAreaId;
    let marketId =
      typeof this.initialSetupForm.value.marketId !== 'undefined' &&
      this.initialSetupForm.value.marketId !== null
        ? this.initialSetupForm.value.marketId.Id
        : 0;
    let marketName =
      typeof this.initialSetupForm.value.marketId !== 'undefined' &&
      this.initialSetupForm.value.marketId !== null
        ? this.initialSetupForm.value.marketId.OfficialName
        : '';
    if (studyData && studyData.DataSetStudyId === 3 && studyData.Id === 26) {
      marketId = 21;
      marketName = 'Europe';
    }

    let analystId: number[] = [];
    let analystName: string[] = [];
    if (this.initialSetupForm.value.analystId.length > 0) {
      this.initialSetupForm.value.analystId.map((single: any) => {
        analystId.push(single.Id);
        analystName.push(single.Name);
      });
    }

    const formData = {
      analystIdList: analystId,
      analystNameList: analystName,
      analystId: 0,
      analystName: '',
      description: this.initialSetupForm.value.description,
      marketId: marketId,
      marketName: marketName,
      name: this.initialSetupForm.value.name,
      projectLeadId: projectLead?.Id,
      projectLead: projectLead?.Name,
      studyDataSetId: this.initialSetupForm.value.studyDataSetId.Id,
      studyDataSetName: this.initialSetupForm.value.studyDataSetId.Name,
      studyProjectId: this.initialSetupForm.value.studyProjectId.Id,
      projectName: this.initialSetupForm.value.studyProjectId.Name,
      studyYear: this.initialSetupForm.value.studyYear,
      prefillFromId:
        this.initialSetupForm.value.preFillForm !== null
          ? this.initialSetupForm.value.preFillForm.Id
          : 0,
      studyAreaId: this.initialSetupForm.value.studyAreaId.Id,
      studyAreaName: this.initialSetupForm.value.studyAreaId.Name,
      id: 0,
      isSignOff: false,
      isMidYear: false,
      lockedForEditBy: 0,
      lockedForEditByName: '',
      csvAnalystIds: '',
      csvAnalystNames: '',
    };

    this.isLoadingService.add();
    this.sharedService.saveInitialSetupData(formData).subscribe(
      (res: ApiResponse) => {
        if (res.HttpStatusCode === 200) {
          // this.alertService.success(res.Data);
          this.submitted = false;
          this.initialSetupForm.reset();
          this.initialSetupForm.updateValueAndValidity();
          this.showSuccessMsg = true;
        } else {
          this.alertService.danger(res.Data);
        }
        this.isLoadingService.remove();
      },
      (err) => {
        const msg =
          typeof err.message !== 'undefined'
            ? err.message
            : 'Something went wrong.';
        this.alertService.danger(msg);
        this.isLoadingService.remove();
      }
    );
  }
}
