import {
  Component,
  Input,
  OnInit,
  SimpleChanges,
  ViewChild,
} from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AlertService } from '@full-fledged/alerts';
import { IsLoadingService } from '@service-work/is-loading';
import { ApiResponse } from 'src/model/apiResponse.model';
import { LubricantDemandDataItem, StudyArea } from 'src/model/studyArea.model';
import { StudyService } from 'src/services/study.service';
import { InitialSetup } from 'src/model/initialSetup.model';
import PivotGridDataSource, {
  Field,
} from 'devextreme/ui/pivot_grid/data_source';

@Component({
  selector: 'app-lubricant-demand-preview',
  templateUrl: './lubricant-demand-preview.component.html',
  styleUrls: ['./lubricant-demand-preview.component.css'],
})
export class LubricantDemandPreviewComponent implements OnInit {
  marketYearCategoryId: number = 0;
  studyProjectMarketYearId: number = 0;
  studyAreaId: number = 0;

  // studyData: LubricantDemandDataItem[] = [];

  private _studyData: LubricantDemandDataItem[] = [];
  @Input() set studyData(v: LubricantDemandDataItem[]) {
    // console.log(v);
    this._studyData = v;
  }

  get studyData(): LubricantDemandDataItem[] {
    return this._studyData;
  }

  @Input() currentYear: number = 0;

  // currentYear: number = 0;
  previousYear1: number = 0;
  previousYear2: number = 0;

  @Input() foreCastYear1: number = 0;
  @Input() foreCastYear2: number = 0;

  volumeYears: any[] = [];

  cagr1: string = '';
  cagr2: string = '';

  studySummary: InitialSetup = new InitialSetup().deserialize({});

  pivotGridDataSource: PivotGridDataSource;
  gridFields: Field[] = [];

  constructor(
    private studyService: StudyService,
    private isLoadingService: IsLoadingService,
    private alertService: AlertService,
    private route: ActivatedRoute,
    private router: Router
  ) {
    const params = this.route.snapshot.params;

    this.marketYearCategoryId = params['mycId'];
    this.studyProjectMarketYearId = params['spmId'];
    this.studyAreaId = params['studyArea'];

    this.pivotGridDataSource = new PivotGridDataSource();
  }

  ngOnInit(): void {
    // this.getLubricantDemandData();
  }

  ngOnChanges(changes: SimpleChanges): void {
    //Called before any other lifecycle hook. Use it to inject dependencies, but avoid any serious work here.
    //Add '${implements OnChanges}' to the class.
    // this.foreCastYear1 = changes['foreCastYear1'].currentValue;

    this.getPreviewData(this.studyData);
  }

  getGridFields() {
    return (this.gridFields = [
      {
        dataField: 'MarketSegment',
        area: 'row',
        caption: 'Market Segment',
        dataType: 'string',
        width: 110,
      },
      {
        dataField: 'ProductCategory',
        area: 'row',
        caption: 'Product Category',
        width: 150,
        dataType: 'string',
      },
      {
        dataField: 'SubProduct',
        caption: 'Sub Product',
        area: 'filter',
        width: 90,
        summaryDisplayMode: 'none',
        dataType: 'string',
      },
      {
        dataField: 'ViscosityGrade',
        area: 'filter',
        caption: 'Viscosity Grade',
        width: 150,
        dataType: 'string',
        summaryDisplayMode: 'none',
      },
      {
        visible: true,
        isCalculated: false,
        calculatedFieldType: 'none',
        id: 6,
        caption: 'Lubricant Demand, KTonnes',
        dataField: 'Volume',
        dataFieldType: 'Volume',
        width: 80,
        dataType: 'number',
        summaryType: 'sum',
        summaryDisplayMode: 'none',
        format: {
          type: 'fixedPoint',
          precision: 1,
        },
        area: 'data',
      },
      {
        visible: true,
        isCalculated: false,
        calculatedFieldType: 'none',
        id: 7,
        caption: 'Year',
        dataField: 'Year',
        width: 80,
        dataType: 'string',
        summaryDisplayMode: 'none',
        area: 'column',
      },
    ] as Field[]);
  }

  getPreviewData(studyData: LubricantDemandDataItem[]) {
    const currentYear = this.currentYear;
    // this.foreCastYear1 = currentYear + 4;
    // this.foreCastYear2 = currentYear + 10;
    // this.foreCastYear2 = this.foreCastYear1 + 5;

    const previewData: {
      MarketSegment: string;
      ProductCategory: string;
      SubProduct: string;
      ViscosityGrade: string;
      Year: number;
      Volume: number;
    }[] = [];
    studyData.map((item: LubricantDemandDataItem) => {
      previewData.push({
        MarketSegment: item.MarketSegment,
        ProductCategory: item.ProductCategory,
        SubProduct: item.SubProduct,
        ViscosityGrade: item.ViscosityGrade,
        Year: currentYear - 5,
        Volume: +item.PreviousYear5,
      });
      previewData.push({
        MarketSegment: item.MarketSegment,
        ProductCategory: item.ProductCategory,
        SubProduct: item.SubProduct,
        ViscosityGrade: item.ViscosityGrade,
        Year: currentYear - 4,
        Volume: +item.PreviousYear4,
      });
      previewData.push({
        MarketSegment: item.MarketSegment,
        ProductCategory: item.ProductCategory,
        SubProduct: item.SubProduct,
        ViscosityGrade: item.ViscosityGrade,
        Year: currentYear - 3,
        Volume: +item.PreviousYear3,
      });
      previewData.push({
        MarketSegment: item.MarketSegment,
        ProductCategory: item.ProductCategory,
        SubProduct: item.SubProduct,
        ViscosityGrade: item.ViscosityGrade,
        Year: currentYear - 2,
        Volume: +item.PreviousYear2,
      });
      previewData.push({
        MarketSegment: item.MarketSegment,
        ProductCategory: item.ProductCategory,
        SubProduct: item.SubProduct,
        ViscosityGrade: item.ViscosityGrade,
        Year: currentYear - 1,
        Volume: +item.PreviousYear1,
      });
      previewData.push({
        MarketSegment: item.MarketSegment,
        ProductCategory: item.ProductCategory,
        SubProduct: item.SubProduct,
        ViscosityGrade: item.ViscosityGrade,
        Year: currentYear,
        Volume: +item.CurrentYear,
      });
      previewData.push({
        MarketSegment: item.MarketSegment,
        ProductCategory: item.ProductCategory,
        SubProduct: item.SubProduct,
        ViscosityGrade: item.ViscosityGrade,
        Year: this.foreCastYear1,
        Volume: +item.Forecast1,
      });
      previewData.push({
        MarketSegment: item.MarketSegment,
        ProductCategory: item.ProductCategory,
        SubProduct: item.SubProduct,
        ViscosityGrade: item.ViscosityGrade,
        Year: this.foreCastYear2,
        Volume: +item.Forecast2,
      });
    });

    this.pivotGridDataSource = new PivotGridDataSource({
      store: previewData,
      fields: this.getGridFields(),
    });
  }

  changeStudyData(studyData: LubricantDemandDataItem[]) {
    this.studyData = studyData;
    this.getPreviewData(this.studyData);
  }

  // not in use
  getLubricantDemandData() {
    this.isLoadingService.add();
    this.studyService
      .getLubricantDemandData(
        this.marketYearCategoryId,
        this.studyProjectMarketYearId
      )
      .subscribe((res: ApiResponse) => {
        if (res.HttpStatusCode === 200) {
          const studyData = res.Data as StudyArea;

          this.studyData =
            studyData.LubricantDemandDataItems as LubricantDemandDataItem[];

          this.studySummary = new InitialSetup().deserialize(
            studyData.InitialSetup
          );

          let currentYear =
            this.studySummary.StudyYear || new Date().getFullYear();

          currentYear = currentYear - 1;

          this.volumeYears = [];
          const myYears = [];
          for (let i = currentYear - 5, j = 5; i < currentYear; i++, j--) {
            this.volumeYears.push({ field: `PreviousYear${j}`, year: i });
            myYears[j] = i;
          }

          this.currentYear = currentYear;
          // this.foreCastYear1 = currentYear + 5;
          this.foreCastYear1 = currentYear + 4;
          // this.foreCastYear2 = currentYear + 10;
          this.foreCastYear2 = this.foreCastYear1 + 5;

          const previewData: {
            MarketSegment: string;
            ProductCategory: string;
            SubProduct: string;
            ViscosityGrade: string;
            Year: number;
            Volume: number;
          }[] = [];
          this.studyData.map((item: LubricantDemandDataItem) => {
            previewData.push({
              MarketSegment: item.MarketSegment,
              ProductCategory: item.ProductCategory,
              SubProduct: item.SubProduct,
              ViscosityGrade: item.ViscosityGrade,
              Year: currentYear - 5,
              Volume: item.PreviousYear5,
            });
            previewData.push({
              MarketSegment: item.MarketSegment,
              ProductCategory: item.ProductCategory,
              SubProduct: item.SubProduct,
              ViscosityGrade: item.ViscosityGrade,
              Year: currentYear - 4,
              Volume: item.PreviousYear4,
            });
            previewData.push({
              MarketSegment: item.MarketSegment,
              ProductCategory: item.ProductCategory,
              SubProduct: item.SubProduct,
              ViscosityGrade: item.ViscosityGrade,
              Year: currentYear - 3,
              Volume: item.PreviousYear3,
            });
            previewData.push({
              MarketSegment: item.MarketSegment,
              ProductCategory: item.ProductCategory,
              SubProduct: item.SubProduct,
              ViscosityGrade: item.ViscosityGrade,
              Year: currentYear - 2,
              Volume: item.PreviousYear2,
            });
            previewData.push({
              MarketSegment: item.MarketSegment,
              ProductCategory: item.ProductCategory,
              SubProduct: item.SubProduct,
              ViscosityGrade: item.ViscosityGrade,
              Year: currentYear - 1,
              Volume: item.PreviousYear1,
            });
            previewData.push({
              MarketSegment: item.MarketSegment,
              ProductCategory: item.ProductCategory,
              SubProduct: item.SubProduct,
              ViscosityGrade: item.ViscosityGrade,
              Year: currentYear,
              Volume: item.CurrentYear,
            });
            previewData.push({
              MarketSegment: item.MarketSegment,
              ProductCategory: item.ProductCategory,
              SubProduct: item.SubProduct,
              ViscosityGrade: item.ViscosityGrade,
              Year: this.foreCastYear1,
              Volume: item.Forecast1,
            });
            previewData.push({
              MarketSegment: item.MarketSegment,
              ProductCategory: item.ProductCategory,
              SubProduct: item.SubProduct,
              ViscosityGrade: item.ViscosityGrade,
              Year: this.foreCastYear2,
              Volume: item.Forecast2,
            });
          });

          this.pivotGridDataSource = new PivotGridDataSource({
            store: previewData,
            fields: this.getGridFields(),
          });

          this.currentYear = currentYear;
          // this.foreCastYear1 = currentYear + 5;
          this.foreCastYear1 = currentYear + 4;
          // this.foreCastYear2 = currentYear + 10;
          this.foreCastYear2 = this.foreCastYear1 + 5;

          this.cagr1 = `${currentYear}-${this.foreCastYear1}`;
          this.cagr2 = `${this.foreCastYear1}-${this.foreCastYear2}`;

          this.volumeYears.push({ field: `CurrentYear`, year: currentYear });

          this.isLoadingService.remove();
        } else {
          this.alertService.danger(res.Data);
        }
      });
  }
}
