import { Injectable } from '@angular/core';
import {
  HttpRequest,
  HttpHandler,
  HttpEvent,
  HttpInterceptor,
} from '@angular/common/http';
import { Observable } from 'rxjs';

import { LocalStorageService } from 'src/services/localStorage.service';
import { Constants } from 'src/app/Constant';
import { UserData } from 'src/model/userData.model';

@Injectable()
export class JwtInterceptor implements HttpInterceptor {
  constructor(private localStorageService: LocalStorageService) {}

  intercept(
    request: HttpRequest<any>,
    next: HttpHandler
  ): Observable<HttpEvent<any>> {
    // add authorization header with jwt token if available
    const currentUserData = this.localStorageService.getData(
      Constants.userDataKey
    ) as UserData;

    if (currentUserData) {
      if (request.url.indexOf(Constants.serverEndpoint) !== -1) {
        request = request.clone({
          setHeaders: {
            Authorization: `Bearer ${currentUserData.idToken}`,
          },
        });
      }
    }

    return next.handle(request);
  }
}
