import { Injectable } from '@angular/core';
import {
  ActivatedRouteSnapshot,
  CanActivate,
  Router,
  RouterStateSnapshot,
} from '@angular/router';
import { LocalStorageService } from 'src/services/localStorage.service';
import { Constants } from 'src/app/Constant';
import { UserData } from 'src/model/userData.model';
import { UserDetail } from 'src/model/userDetail.model';

@Injectable()
export class AuthGuard implements CanActivate {
  constructor(
    private router: Router,
    private localStorageService: LocalStorageService
  ) {}

  canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {
    const currentUser = this.localStorageService.getData(
      Constants.userDataKey
    ) as UserData;

    if (typeof currentUser.idToken !== 'undefined') {
      // loggedIn so return true
      const currentUserDetail: UserDetail = this.localStorageService.getData(
        Constants.userDetailKey
      );
      const userRole = currentUserDetail.IsManager ? 'MANAGER' : 'ANALYST';
      if (route.data['role'] && route.data['role'].indexOf(userRole) === -1) {
        // console.log('call here');
        this.router.navigate(['study-summary']);
        return false;
      }
      return true;
    } else {
      // not logged in so redirect to login page
      // console.log('call here 123');
      this.router.navigate(['login']);
      return false;
    }
  }
}
