import { Injectable } from '@angular/core';
import { Constants } from 'src/app/Constant';
import { HttpHeaders, HttpClient, HttpParams } from '@angular/common/http';
import { throwError, Observable } from 'rxjs';
import { catchError, map } from 'rxjs/operators';
import { ApiResponse } from 'src/model/apiResponse.model';

@Injectable({
  providedIn: 'root',
})
export class SharedService {
  apiEndpoint: string = '';
  constructor(private http: HttpClient) {
    this.apiEndpoint = Constants.apiEndpoint;
  }

  getStudyArea() {
    return this.http.get<ApiResponse>(`${this.apiEndpoint}/StudyDataSet`).pipe(
      map((data) => {
        return data;
      })
    );
  }

  getStudyAreaCategory(dataSetId: number) {
    const params = new HttpParams().append('DataSetId', dataSetId);
    return this.http
      .get<ApiResponse>(`${this.apiEndpoint}/StudyArea/StudyArea`, {
        params,
      })
      .pipe(
        map((data) => {
          return data;
        })
      );
  }

  getProject(dataSetId: number, year: number) {
    const params = new HttpParams()
      .append('DataSetId', dataSetId)
      .append('studyYear', year);
    return this.http
      .get<ApiResponse>(`${this.apiEndpoint}/Project`, {
        params,
      })
      .pipe(
        map((data) => {
          return data;
        })
      );
  }

  getProjectLead() {
    return this.http.get<ApiResponse>(`${this.apiEndpoint}/ProjectLead`).pipe(
      map((data) => {
        return data;
      })
    );
  }

  getMarket(dataSetId: number, year: number) {
    const params = new HttpParams()
      .append('DataSetId', dataSetId)
      .append('BaseTimeYear', year);
    return this.http
      .get<ApiResponse>(`${this.apiEndpoint}/Market`, { params })
      .pipe(
        map((data) => {
          return data;
        })
      );
  }

  getAnalyst() {
    return this.http.get<ApiResponse>(`${this.apiEndpoint}/Analysts`).pipe(
      map((data) => {
        return data;
      })
    );
  }

  getInitialSetupByMarketId(marketId: number, studyArea: number) {
    const params = new HttpParams()
      .append('marketId', marketId)
      .append('DataItemCategoryId', studyArea);
    return this.http
      .get<ApiResponse>(
        `${this.apiEndpoint}/InitialSetup/InitialSetupByMarketId`,
        {
          params,
        }
      )
      .pipe(
        map((data) => {
          return data;
        })
      );
  }

  getInitialSetupById(Id: any, studyAreaId: number) {
    const params = new HttpParams().append('DataItemCategoryId', studyAreaId);
    return this.http
      .get<ApiResponse>(
        `${this.apiEndpoint}/InitialSetup/InitialSetupById/${Id}`,
        {
          params,
        }
      )
      .pipe(
        map((data) => {
          return data;
        })
      );
  }

  getStudyAreaSummaryByStudyMarketYearId(marketYearId: any) {
    const params = new HttpParams().append('studyMarketYearId', marketYearId);
    return this.http
      .get<ApiResponse>(`${this.apiEndpoint}/StudyArea/GetSummary`, {
        params,
      })
      .pipe(
        map((data) => {
          return data;
        })
      );
  }

  getStudyAreaSummary() {
    return this.http
      .get<ApiResponse>(`${this.apiEndpoint}/StudyArea/Summary`)
      .pipe(
        map((data) => {
          return data;
        })
      );
  }

  setLockUnlockStudy(marketYearCategoryDataId: number, isLocked: boolean) {
    const formData = {
      marketYearCategoryDataId: marketYearCategoryDataId,
      acquireLock: isLocked,
    };
    return this.http
      .post<ApiResponse>(
        `${this.apiEndpoint}/MarketYearCategoryData/LockForEdit`,
        formData
      )
      .pipe(
        map((data) => {
          return data;
        })
      );
  }

  requestForUnlockStudy(marketYearCategoryDataId: number, formData: any) {
    return this.http
      .post<ApiResponse>(
        `${this.apiEndpoint}/MarketYearCategoryData/SendEmailToRevokeEditLock?MarketYearCategoryDataId=${marketYearCategoryDataId}`,
        formData
      )
      .pipe(
        map((data) => {
          return data;
        })
      );
  }

  migrateDataFromAnotherMarket(
    MarketYearCategoryDataId: number,
    TargetMarketId: number
  ) {
    const formData = {};
    return this.http
      .post<ApiResponse>(
        `${this.apiEndpoint}/MarketYearCategoryData/MigrateData?MarketYearCategoryDataId=${MarketYearCategoryDataId}&TargetMarketId=${TargetMarketId}`,
        formData
      )
      .pipe(
        map((data) => {
          return data;
        })
      );
  }

  removeStudyAreaMaster(
    dataSetId: number,
    dataItemCategoryId: number,
    marketYearDataCategoryId: number,
    studyAreaId: number
  ) {
    const params = new HttpParams()
      .append('DataSetId', dataSetId)
      .append('DataItemCategoryId', dataItemCategoryId)
      .append('MarketYearCategoryDataId', marketYearDataCategoryId)
      .append('StudyAreaId', studyAreaId);

    return this.http
      .delete<ApiResponse>(`${this.apiEndpoint}/MasterDataEntry/MasterStudy`, {
        params,
        headers: {
          'Content-Type': 'application/json',
        },
      })
      .pipe(
        map((data) => {
          return data;
        })
      );
  }

  removeStudyDataRow(
    dataSetId: number,
    dataItemCategoryId: number,
    marketYearDataCategoryId: number,
    dataRowId: string
  ) {
    const params = new HttpParams()
      .append('DataSetId', dataSetId)
      .append('DataItemCategoryId', dataItemCategoryId)
      .append('MarketYearCategoryDataId', marketYearDataCategoryId)
      .append('DataRowId', dataRowId.toString());

    return this.http
      .delete<ApiResponse>(
        `${this.apiEndpoint}/MasterDataEntry/MasterDataRow`,
        {
          params,
          headers: {
            'Content-Type': 'application/json',
          },
        }
      )
      .pipe(
        map((data) => {
          return data;
        })
      );
  }

  getMarketSegment() {
    return this.getDataEntryData('MarketSegments');
  }

  getSupplier() {
    return this.getDataEntryData('Suppliers');
  }

  getProductCategories() {
    return this.getDataEntryData('ProductCategories');
  }

  getSubProducts() {
    return this.getDataEntryData('SubProducts');
  }

  getViscosityGrades() {
    return this.getDataEntryData('ViscosityGrades');
  }

  saveInitialSetupData(formData: any) {
    return this.http
      .post<ApiResponse>(
        `${this.apiEndpoint}/InitialSetup/InitialSetup`,
        formData,
        {
          headers: {
            'Content-Type': 'application/json',
          },
        }
      )
      .pipe(
        map((data) => {
          return data;
        })
      );
  }

  getSPSegments(MarketId: number) {
    return this.getDataEntryData('Segments', [
      { key: 'MarketId', value: MarketId },
    ]);
  }

  getSyntheticType() {
    return this.getDataEntryData('SyntheticType');
  }

  getVehicleTypes() {
    return this.getDataEntryData('VehicleType');
  }

  getDataEntryData(type: string, params: any = []) {
    let _params = new HttpParams();
    if (params.length > 0) {
      params.map((item: any) => {
        _params = _params.append(item.key, item.value);
      });
    }
    return this.http
      .get<ApiResponse>(`${this.apiEndpoint}/DataEntry/${type}`, {
        params: _params,
      })
      .pipe(
        map((data) => {
          return data;
        })
      );
  }

  getAllEngineOilType() {
    return this.http
      .get<ApiResponse>(
        `${this.apiEndpoint}/DataEntry/SCSSEngineOilTypeProductCategory`
      )
      .pipe(
        map((data) => {
          return data;
        })
      );
  }

  getSEDataEntryData(type: string, params: any = []) {
    let _params = new HttpParams();
    if (params.length > 0) {
      params.map((item: any) => {
        _params = _params.append(item.key, item.value);
      });
    }
    return this.http
      .get<ApiResponse>(
        `${this.apiEndpoint}/SpecialtyExcipient/DataEntrySEAP/${type}`,
        {
          params: _params,
        }
      )
      .pipe(
        map((data) => {
          return data;
        })
      );
  }

  getPSCDataEntryData(type: string, params: any = []) {
    let _params = new HttpParams();
    if (params.length > 0) {
      params.map((item: any) => {
        _params = _params.append(item.key, item.value);
      });
    }
    return this.http
      .get<ApiResponse>(`${this.apiEndpoint}/DataEntryPSC/${type}`, {
        params: _params,
      })
      .pipe(
        map((data) => {
          return data;
        })
      );
  }

  getSHCDataEntryData(type: string, params: any = []) {
    let _params = new HttpParams();
    if (params.length > 0) {
      params.map((item: any) => {
        _params = _params.append(item.key, item.value);
      });
    }
    return this.http
      .get<ApiResponse>(`${this.apiEndpoint}/DataEntrySHC/${type}`, {
        params: _params,
      })
      .pipe(
        map((data) => {
          return data;
        })
      );
  }

  getDataRelationsData() {
    return this.http
      .get<ApiResponse>(`${this.apiEndpoint}/MasterDataEntry/DataRelations`)
      .pipe(
        map((data) => {
          return data;
        })
      );
  }

  saveNewDataEntry(dataRelationId: number, newName: string) {
    const formData = {
      dataRelationId,
      newName,
    };
    return this.http
      .post<ApiResponse>(
        `${this.apiEndpoint}/MasterDataEntry/NewDataEntry`,
        formData,
        {
          headers: {
            'Content-Type': 'application/json',
          },
        }
      )
      .pipe(
        map((data) => {
          return data;
        })
      );
  }

  getEuropeanCountryLists() {
    return this.http
      .get<ApiResponse>(`${this.apiEndpoint}/Market/EuropeanCountries`)
      .pipe(
        map((data) => {
          return data;
        })
      );
  }

  getPivotSettings(viewId: number) {
    return this.http
      .get<ApiResponse>(
        `${this.apiEndpoint}/MasterDataEntry/PivotGridSettings?ViewId=${viewId}`
      )
      .pipe(
        map((data) => {
          return data;
        })
      );
  }

  getDataEntryDropDowns(dataRelationId: number) {
    return this.http
      .get<ApiResponse>(
        `${this.apiEndpoint}/MasterDataEntry/DataEntryDropDowns?DataRelationId=${dataRelationId}`,
        {
          headers: {
            'Content-Type': 'application/json',
          },
        }
      )
      .pipe(
        map((data) => {
          return data;
        })
      );
  }

  getUnitOfMeasure() {
    return this.http
      .get<ApiResponse>(`${this.apiEndpoint}/MasterDataEntry/UnitOfMeasures`)
      .pipe(
        map((data) => {
          return data;
        })
      );
  }

  getVolumeUnits(dataSetId: number): Observable<ApiResponse> {
    return this.http.get<ApiResponse>(`${this.apiEndpoint}/Common/VolumeUnits?DataSetId=${dataSetId}`)
  }

  getCurrencies(dataSetId: number): Observable<ApiResponse> {
    return this.http
      .get<ApiResponse>(`${this.apiEndpoint}/Common/Currencies?DataSetId=${dataSetId}`)
  }

  getSubMarketDropDowns(datasetId: number, marketId: number) {
    return this.http
      .get<ApiResponse>(
        `${this.apiEndpoint} /Market/SubMarkets ? DataSetId = ${datasetId}& MarketId=${marketId} `,
        {
          headers: {
            'Content-Type': 'application/json',
          },
        }
      )
      .pipe(
        map((data) => {
          return data;
        })
      );
  }

  getSubMarkets(datasetId: number, marketId: number) {
    return this.http
      .get<ApiResponse>(
        `${this.apiEndpoint} /Market/SubMarkets ? DataSetId = ${datasetId}& MarketId=${marketId} `
      )
      .pipe(
        map((data) => {
          return data;
        })
      );
  }

  getUnitsOfMeasureDropDowns() {
    return this.http
      .get<ApiResponse>(`${this.apiEndpoint} /MasterDataEntry/UnitOfMeasures`, {
        headers: {
          'Content-Type': 'application/json',
        },
      })
      .pipe(
        map((data) => {
          return data;
        })
      );
  }

  getPivotState(
    viewId: number,
    isDataEntryGrid: boolean = false
  ): Observable<any> {
    return this.http
      .get<ApiResponse>(
        `${this.apiEndpoint}/PivotState?viewId=${viewId}&IsDataEntryGrid=${isDataEntryGrid}`
      )
      .pipe(
        map((data) => {
          return data;
        })
      );
  }

  setPivotState(
    viewId: number,
    userId: number,
    isDefault: boolean,
    viewState: string,
    isDataEntryGrid: boolean = false
  ): Observable<boolean> {
    let headers = {
      'Content-Type': 'application/json',
    };
    let url: string = this.apiEndpoint + '/PivotState/SaveState';
    let body = {
      userId: userId,
      viewId: viewId,
      isDefault: isDefault,
      StateData: viewState,
      isDataEntryGrid: isDataEntryGrid,
    };
    return this.http.post<boolean>(url, body, { headers });
  }

  deletePivotState(
    viewId: number,
    pivotStateId: number,
    userId: number = 0
  ): Observable<string> {
    let headers = {
      'Content-Type': 'application/json',
    };
    const params = new HttpParams()
      .append('viewId', viewId)
      .append('pivotStateId', pivotStateId);
    return this.http.delete<any>(`${this.apiEndpoint}/PivotState/DeleteState`, {
      params,
      headers,
    });
  }


  getState(
    viewId: number,
    marketYearCategoryId: number,
    isDataEntryGrid: boolean = false
  ): Observable<any> {
    return this.http
      .get<ApiResponse>(
        `${this.apiEndpoint}/PivotState?viewId=${viewId}&marketYearCategoryId=${marketYearCategoryId}&IsDataEntryGrid=${isDataEntryGrid}`
      )
      .pipe(
        map((data) => {
          return data;
        })
      );
  }

  setState(
    viewId: number,
    marketYearCategoryId: number,
    userId: number,
    isDefault: boolean,
    viewState: string,
    isDataEntryGrid: boolean = false
  ): Observable<boolean> {
    let headers = {
      'Content-Type': 'application/json',
    };
    let url: string = this.apiEndpoint + '/PivotState/SaveState';
    let body = {
      userId: userId,
      viewId: viewId,
      marketYearCategoryId: marketYearCategoryId,
      isDefault: isDefault,
      StateData: viewState,
      isDataEntryGrid: isDataEntryGrid,
    };
    return this.http.post<boolean>(url, body, { headers });
  }

  deleteState(
    viewId: number,
    marketYearCategoryId: number,
    pivotStateId: number,
    userId: number = 0
  ): Observable<string> {
    let headers = {
      'Content-Type': 'application/json',
    };
    const params = new HttpParams()
      .append('viewId', viewId)
      .append('marketYearCategoryId', marketYearCategoryId)
      .append('pivotStateId', pivotStateId);
    return this.http.delete<any>(`${this.apiEndpoint}/PivotState/DeleteState`, {
      params,
      headers,
    });
  }

  getDependentDataEntryDropDowns(dataRelationId: number, dependentField: any) {
    return this.http
      .post<ApiResponse>(
        `${this.apiEndpoint}/MasterDataEntry/DataEntryDependentDropDowns?dataRelationId=${dataRelationId}`,
        dependentField,
        {
          headers: {
            'Content-Type': 'application/json',
          },
        }
      )
      .pipe(
        map((data) => {
          return data;
        })
      );
  }
}
