import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { MsalService } from '@azure/msal-angular';
import { AuthenticationResult } from '@azure/msal-browser';
import { IsLoadingService } from '@service-work/is-loading';
import { Observable } from 'rxjs';
import { ApiResponse } from 'src/model/apiResponse.model';
import { UserData } from 'src/model/userData.model';
import { UserDetail } from 'src/model/userDetail.model';
import { LocalStorageService } from 'src/services/localStorage.service';
import { UserService } from 'src/services/user.service';
import { Constants } from './Constant';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
})
export class AppComponent implements OnInit {
  title = 'KlineCoreUI';
  isLoading!: Observable<boolean>;
  constructor(
    private authService: MsalService,
    private localStorageService: LocalStorageService,
    private route: Router,
    private userService: UserService,
    private isLoadingService: IsLoadingService
  ) {
    // this.isLoading = this.isLoadingService.isLoading$();
  }
  ngOnInit(): void {
    setTimeout(() => {
      this.isLoading = this.isLoadingService.isLoading$();
    }, 0);
    const currentUser = this.localStorageService.getData(
      Constants.userDataKey
    ) as UserData;

    const currentUserData = this.localStorageService.getData(
      Constants.userDetailKey
    );

    if (
      typeof currentUser.idToken === 'undefined' ||
      typeof currentUserData.EmailAddress === 'undefined'
    ) {
      this.authService.instance.handleRedirectPromise().then((res) => {
        if (res != null && res.account != null) {
          this.authService.instance.setActiveAccount(res.account);
          // console.log(res);
          this.localStorageService.setData(
            {
              userName: res.account.username,
              token: res.accessToken,
              idToken: res.idToken,
            },
            Constants.userDataKey
          );
          this.getUserDetail(res.account.username);
        } else {
          this.route.navigate(['login']);
        }
      });
    }
  }

  isLoggedIn(): boolean {
    return this.authService.instance.getActiveAccount() != null;
  }

  login() {
    this.authService.loginRedirect();
  }

  logout() {
    this.authService.logout();
  }

  getUserDetail(emailAddress: string) {
    this.userService.getUserDetail(emailAddress).subscribe(
      (res: ApiResponse) => {
        if (res.HttpStatusCode === 200) {
          const userDetail = new UserDetail().deserialize(res.Data);
          this.localStorageService.setData(userDetail, Constants.userDetailKey);

          this.route.navigate(['study-summary']);
        }
      },
      (err) => {
        console.log(err);
      }
    );
  }
}
