import { CurrencyConversionTypeEnum } from './enums/currencyConversionType.enum';
import { PivotGridDataFieldType } from './enums/pivotGridDataFieldType.enum';
import { CalculatedFieldType } from './enums/calculatedFieldType.enum';
import * as Moment from 'moment';
import { HiddenFilterType } from './enums/hiddenFilterType.enum';
import { SEDataEntry } from './dataEntrySE.model';

export class PivotGridOptions {
  viewId: number = 0;
  databaseId: number = 0;
  industryId: number = 0;
  settings: Settings = new Settings();
  pivotGridSource: PivotGridSource = new PivotGridSource();
  pivotGridDefaultValues: DefaultPivotValues = new DefaultPivotValues();
  canSaveUserState: boolean = true;
  canSaveDefaultStateAndOptions: boolean = false;
}

export class Settings {
  pivotGridTextKeys: PivotGridTextKeys = new PivotGridTextKeys();
  fieldPanelSettings: FieldPanelSettings = new FieldPanelSettings();
  export: Export = new Export();
  charts: Charts = new Charts();
  calculatedFields: CalculatedFields = new CalculatedFields();
  allowSorting: boolean = true;
  allowSortingBySummary: boolean = true;
  allowFiltering: boolean = true;
  allowExpandAll: boolean = true;
  showBorders: boolean = true;
  showColumnGrandTotals: boolean = true;
  showRowGrandTotals: boolean = true;
  showRowTotals: boolean = true;
  showColumnTotals: boolean = true;
  fieldChooser: boolean = true;
  showTotalsPrior: string = 'both';
  scroolingMode: string = 'virtual';
  useNative: boolean = false;
  scrollByContent: boolean = false;
  menuSettings: MenuSettings = new MenuSettings();
  specialSorting: SpecialSorting[] = [];
  rowHeaderLayout: string = 'standard';
  isLatestAccessibleYearUsed: boolean = true;
  showRangeInYearSelectionButton: boolean = false;
  reducedDataSource: ReducedDataSource = new ReducedDataSource();
  useDefaultSorting: boolean = false;
  isCube: boolean = false;
  cubeSettings: CubeSettings = new CubeSettings();
  groupedFields: GroupedField[] = [];
}

export class PivotGridTextKeys {
  pivotHeader: string = '';
  pivotDescription: string = '';
  pivotCurrencyHeader: string = 'KEY_PivotGrid_DefaultText_Currency';
  pivotConversionHeader: string =
    'KEY_PivotGrid_DefaultText_CurrencyConversionType';
  pivotUnitsOfVolumeHeader: string = 'KEY_PivotGrid_DefaultText_UnitsOfVolume';
  pivotUnitsOfMassHeader: string = 'KEY_PivotGrid_DefaultText_UnitsOfMass';
  pivotParametersHeader: string = 'KEY_PivotGrid_DefaultText_Parameters';
  pivotYearsHeader: string = 'KEY_PivotGrid_DefaultText_BasePeriod';
  pivotNoDataMessage: string = 'KEY_PivotGrid_DefaultText_MissingData';
}

export class SpecialSorting {
  fieldName: string = '';
  beginningWith: string = '';
}

export class ReducedDataSource {
  isReducedDataSource = false;
  selectedYearAfter = 5;
}

export class FieldPanelSettings {
  visible: boolean = true;
  showColumnFields: boolean = true;
  showDataFields: boolean = true;
  showFilterFields: boolean = true;
  showRowFields: boolean = true;
  allowFieldDragging: boolean = true;
}

export class Export {
  enabled: boolean = true;
  ignoreExcelErrors: boolean = true;
  fileName: string = 'KlineExcelExport';
}

export class CalculatedFields {
  formatPrecision: number = 1;
  isPercentOfTotal: boolean = true;
  isPercentOfChange: boolean = true;
  isCagr: boolean = true;
}

export class Charts {
  isVisible: boolean = false;
  isRotated: boolean = false;
  inverted: boolean = false;
  alternateDataFields: boolean = true;
  putDataFieldsInto: string = 'series';
}

export class PivotGridSource {
  fields: Field[] = [];
  store: any = null;
}

export class Format {
  type: string = '';
  precision: number = 0;
}

export class Field {
  id: number = 0;
  visible = true;
  caption: string = '';
  captionWithPlaceholder = '';
  width: number = 0;
  dataField: string = '';
  dataFieldType: PivotGridDataFieldType | undefined;
  area: string = '';
  dataType: string = '';
  summaryType: string = '';
  summaryDisplayMode: string = '';
  format: Format | undefined;
  sortOrder: any;
  sortingMethod: any;
  calculateCustomSummary: any = undefined;
  calculateSummaryValue: any = undefined;
  customizeText: any;
  isMeasure: boolean = false;
  isCalculated = false;
  calculatedFieldType: CalculatedFieldType = CalculatedFieldType.none;
  groupName: string | undefined;
  groupIndex: number | undefined;
  filterValues: string[] | undefined;
  hiddenFilterType: HiddenFilterType | undefined = undefined;
}

export class GroupedField {
  groupName: string = '';
  area: string = '';
}

export class DefaultPivotValues {
  currency: any = {
    Id: 27,
    IsActive: false,
    Name: 'USD',
    CurrencyCode: 'USD',
    CurrencyName: 'USD',
    Code: 'USD',
  };
  currencyConversionType: CurrencyConversionTypeEnum =
    CurrencyConversionTypeEnum.Constant;
  // latest accessible year and unit of measure must load from the database
}

export class MenuSettings {
  isChart: boolean = true;
  isCurrencySelection: boolean = true;
  isCurrencyConversion: boolean = true;
  isUnitsOfVolume: boolean = false;
  isUnitsOfMass: boolean = true;
  isYear: boolean = true;
  isCalculatedFieldsSelection: boolean = true;
  isParametersSelection: boolean = false;
}

export class CubeSettings {
  catalog: string = 'KlinePro_US_Test';
  cube: string = 'RetailProjected';
}
