import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { Observable, Subject, takeUntil } from 'rxjs';
import { ApiResponse } from 'src/model/apiResponse.model';
import { CalculatedFieldType } from 'src/model/enums/calculatedFieldType.enum';
import { CurrencyConversionTypeEnum } from 'src/model/enums/currencyConversionType.enum';
import { PivotGridOptions } from 'src/model/pivotGridOptions.model';
import { CurrencyDropdown, UserStateAndSetttings } from 'src/model/userStateAndSetttings.model';
import { SharedService } from 'src/services/shared.service';
import { DropdownCurrency } from '../../../../model/dropdownCurrency.model';
import { DropdownUnits } from '../../../../model/dropdownUnits.model';
import { SlideInOutAnimation } from '../../../animations';

declare let $: any;
@Component({
  selector: 'pivot-grid-preview-top-menu',
  templateUrl: './pivot-grid-preview-top-menu.component.html',
  styleUrls: ['./pivot-grid-preview-top-menu.component.css'],
  animations: [SlideInOutAnimation],
})
export class PivotGridPreviewTopMenuComponent implements OnInit {
  calculatedFieldSelectorClicked: any;
  intervalCalculatedField: any;

  selectedCurrency: DropdownCurrency = new DropdownCurrency();
  selectedMassUnit: DropdownUnits = new DropdownUnits();

  studySetup: any;

  @Input() set setSelectedCurrency(data: DropdownCurrency) {
    if (data) {
      this.selectedCurrency = data;
    }
  }

  @Input() set setSelectedMassUnit(data: DropdownUnits) {
    if (data) {
      this.selectedMassUnit = data;
    }
  }

  @Input() set setCurrency(data: any) {
    this.currencyDropdown = data;
  }

  @Input() set setVolumeUnits(data: any) {
    this.massUnitDropdown = data;
  }

  @Input() set pivotGridOptions(value: PivotGridOptions) {
    this.pivotGridOptionsValues = value;
    this.autoSaveStateStatus = this.getAutoSaveStateStatus(
      this.pivotGridOptionsValues,
      this.pivotGridstateValues
    );
  }

  @Input() set setStudySetup(studySetup: any) {
    if (studySetup) {
      this.studySetup = studySetup;
      this.setTopMenuValues();
    }
  }
  // @Input() pivotCalculatedFields: Observable<any>;

  @Output() currencySelectionChanged = new EventEmitter<DropdownCurrency>();
  @Output() currencyConversionSelectionChanged = new EventEmitter<number>();
  @Output() massUnitSelectionChanged = new EventEmitter<DropdownUnits>();
  @Output() openAddCalculatedFieldChanged = new EventEmitter<number>();
  @Output() calculatedFieldItemSelected = new EventEmitter<any>();
  @Output() onExportToExcel = new EventEmitter<boolean>();
  @Output() onExportToCvs = new EventEmitter<boolean>();
  @Output() onExportSnapshot = new EventEmitter<boolean>();
  @Output() onChangeAutoSaveStatus = new EventEmitter<boolean>();

  private _destroyed$ = new Subject();

  othersSelectorVisible: boolean = false;
  timePeriodSelectorVisible: boolean = false;
  massUnitSelectorVisible: boolean = false;
  volumeUnitSelectorVisible: boolean = false;
  currencyConversionSelectorVisible: boolean = false;
  currencySelectorVisible: boolean = false;
  parametersSelectorVisible: boolean = false;
  othersSelectorClicked: boolean = false;
  timePeriodSelectorClicked: boolean = false;
  massUnitSelectorClicked: boolean = false;
  volumeUnitSelectorClicked: boolean = false;
  currencyConversionSelectorClicked: boolean = false;
  currencySelectorClicked: boolean = false;
  parametersSelectorClicked: boolean = false;

  exportToExcelSelectorVisible = false;
  exportToExcelSelectorClicked = false;
  exportTypeDropdownSelected: any;

  currencyDropdownSelected: any = undefined;
  unitOfMassDropdownSelected: any = undefined;
  unitOfVolumeDropdownSelected: any = undefined;
  pivotWidth: number = 0;
  currencyDropdown: DropdownCurrency[] = [];
  massUnitDropdown: DropdownUnits[] = [];
  volumeUnitDropdown: any[] = [];
  timePeriod: any[] = [];
  timePeriodFrom: number = 0;
  timePeriodTo: number = 0;
  currencyConversionType: string[] = ['Current', 'Constant'];
  currencyConversionTypeSelected: string = 'Constant';
  exportType = [
    { id: 1, value: 'Export All Data' },
    { id: 2, value: 'Export All Data to CSV' },
    { id: 3, value: 'Export Snapshot' },
  ];
  parametersList: any[] = [];
  eventsCustomActionSubscription: any;
  latestStateData: any;
  latestStateAndSettingsData: any;
  allStateDataList = [];
  activeStateDataList = [];
  isUndone = false;
  count = 0;
  isButtonPressed = false;
  pivotGridOptionsValues: any = undefined;
  pivotGridstateValues: any = undefined;
  calculateFieldsItems: any[] = [];
  calculatedFieldCaption = '';
  calculatedFieldVisible = false;

  dataLoadingIndicatorVisible = false;

  calculatedItems: any[] = [
    { name: CalculatedFieldType.percentOfTotal, value: 'percentOfTotal' },
    { name: CalculatedFieldType.changePercent, value: 'changePercent' },
    { name: CalculatedFieldType.cagr, value: 'cagr' },
  ];

  autoSaveStateStatus: boolean = false;

  constructor(private sharedService: SharedService) { }

  ngOnInit(): void {
  }

  public setTopMenuValues() {
    this.currencyDropdownSelected = this.selectedCurrency;
    this.unitOfMassDropdownSelected = this.selectedMassUnit;
  }

  onHover(item: string) {
    switch (item) {
      case 'othersSelector':
        if (this.othersSelectorClicked) {
          break;
        }
        this.othersSelectorVisible = !this.othersSelectorVisible;
        break;
      case 'timePeriodSelector':
        if (this.timePeriodSelectorClicked) {
          break;
        }
        this.timePeriodSelectorVisible = !this.timePeriodSelectorVisible;
        break;
      case 'massUnitSelector':
        if (this.massUnitSelectorClicked) {
          break;
        }
        this.massUnitSelectorVisible = !this.massUnitSelectorVisible;
        break;
      case 'volumeUnitSelector':
        if (this.volumeUnitSelectorClicked) {
          break;
        }
        this.volumeUnitSelectorVisible = !this.volumeUnitSelectorVisible;
        break;
      case 'currencyConversionSelector':
        if (this.currencyConversionSelectorClicked) {
          break;
        }
        this.currencyConversionSelectorVisible =
          !this.currencyConversionSelectorVisible;
        break;
      case 'calculateFieldSelector':
        if (this.calculatedFieldSelectorClicked) {
          break;
        }
        this.calculatedFieldVisible = !this.calculatedFieldVisible;
        break;
      case 'currencySelector':
        if (this.currencySelectorClicked) {
          break;
        }
        this.currencySelectorVisible = !this.currencySelectorVisible;
        break;
      case 'parametersSelector':
        if (this.parametersSelectorClicked) {
          break;
        }
        this.parametersSelectorVisible = !this.parametersSelectorVisible;
        break;
      case 'exportToExcel':
        if (this.exportToExcelSelectorClicked) {
          break;
        }
        this.exportToExcelSelectorVisible = true;
        break;
    }
  }

  onLeave(item: string) {
    switch (item) {
      case 'othersSelector':
        if (this.othersSelectorClicked) {
          break;
        }
        this.othersSelectorVisible = false;
        break;
      case 'timePeriodSelector':
        if (this.timePeriodSelectorClicked) {
          break;
        }
        this.timePeriodSelectorVisible = false;
        break;
      case 'massUnitSelector':
        if (this.massUnitSelectorClicked) {
          break;
        }
        this.massUnitSelectorVisible = false;
        break;
      case 'volumeUnitSelector':
        if (this.volumeUnitSelectorClicked) {
          break;
        }
        this.volumeUnitSelectorVisible = false;
        break;
      case 'currencyConversionSelector':
        if (this.currencyConversionSelectorClicked) {
          break;
        }
        this.currencyConversionSelectorVisible = false;
        break;
      case 'calculateFieldSelector':
        if (this.calculatedFieldSelectorClicked) {
          break;
        }
        this.calculatedFieldVisible = false;
        break;
      case 'currencySelector':
        if (this.currencySelectorClicked) {
          break;
        }
        this.currencySelectorVisible = false;
        break;
      case 'parametersSelector':
        if (this.parametersSelectorClicked) {
          break;
        }
        this.parametersSelectorVisible = false;
        break;
      case 'parametersSelector':
        if (this.parametersSelectorClicked) {
          break;
        }
        this.parametersSelectorVisible = false;
        break;
      case 'exportToExcel':
        if (this.exportToExcelSelectorClicked) {
          break;
        }
        this.exportToExcelSelectorVisible = false;
        break;
    }
  }

  calculatedFieldOnHover(id: string) {
    for (let i = 0; i <= this.calculateFieldsItems.length;) {
      let elem = 'calculated-' + i;
      if (elem === id) {
        $('#' + id).addClass('show');
      } else {
        $('#' + elem).removeClass('show');
      }
      i++;
    }
  }

  calculatedFieldOnLeave(id: string) {
    $('#' + id).removeClass('show');
  }

  onCalcItemSelected(itemName: string, dataField: string) {
    this.calculatedFieldItemSelected.emit({ itemName, dataField });
  }

  public getButtonStatus(status: boolean): string {
    return status ? 'success' : 'normal';
  }

  getWidth(elementName: string): number {
    if (this.pivotWidth > 0 && this.pivotWidth < 700) {
      return this.pivotWidth;
    }
    switch (elementName) {
      case 'othersSelectorBtn':
        return 45;
      case 'chartBtn':
        return 45;
      case 'expandCollapseBtn':
        return 45;
      case 'currencySelectorBtn':
        return 100;
      case 'currencyConversionSelectorBtn':
        return 130;
      case 'massUnitSelectorBtn':
        return 175;
      case 'volumeUnitSelectorBtn':
        return 175;
      case 'timePeriodSelectorBtn':
        return 180;
      case 'parameters':
        return 190;
      default:
        return 100;
    }
  }

  toggleMenuItem(item: string, isClick: boolean) {
    switch (item) {
      case 'othersSelector':
        if (isClick) {
          this.othersSelectorClicked = !this.othersSelectorClicked;
          // this.getStateSaveByMe(this.viewId, this.userId);
          // this.getStateSharedWithMe(this.viewId, this.userId);
        }
        if (this.othersSelectorClicked) {
          this.othersSelectorVisible = true;
          break;
        }
        this.othersSelectorVisible = !this.othersSelectorVisible;
        break;
      case 'timePeriodSelector':
        if (isClick) {
          this.timePeriodSelectorClicked = !this.timePeriodSelectorClicked;
        }
        if (this.timePeriodSelectorClicked) {
          this.timePeriodSelectorVisible = true;
          break;
        }
        this.timePeriodSelectorVisible = !this.timePeriodSelectorVisible;
        break;
      case 'volumeUnitSelector':
        if (isClick) {
          this.volumeUnitSelectorClicked = !this.volumeUnitSelectorClicked;
        }
        if (this.volumeUnitSelectorClicked) {
          this.volumeUnitSelectorVisible = true;
          break;
        }
        this.volumeUnitSelectorVisible = !this.volumeUnitSelectorVisible;
        break;
      case 'massUnitSelector':
        if (isClick) {
          this.massUnitSelectorClicked = !this.massUnitSelectorClicked;
        }
        if (this.massUnitSelectorClicked) {
          this.massUnitSelectorVisible = true;
          break;
        }
        this.massUnitSelectorVisible = !this.massUnitSelectorVisible;
        break;
      case 'currencyConversionSelector':
        if (isClick) {
          this.currencyConversionSelectorClicked =
            !this.currencyConversionSelectorClicked;
        }
        if (this.currencyConversionSelectorClicked) {
          this.currencyConversionSelectorVisible = true;
          break;
        }
        this.currencyConversionSelectorVisible =
          !this.currencyConversionSelectorVisible;
        break;
      case 'calculateFieldSelector':
        // this.openAddCalculatedField();
        if (isClick) {
          this.calculatedFieldSelectorClicked =
            !this.calculatedFieldSelectorClicked;
        }
        if (this.calculatedFieldSelectorClicked) {
          // this.intervalCalculatedField = setInterval(
          //   () => this.openAddCalculatedField(),
          //   50
          // );
          this.calculatedFieldVisible = true;
          break;
        }
        this.calculatedFieldVisible = !this.calculatedFieldVisible;
        clearInterval(this.intervalCalculatedField);
        break;
      case 'currencySelector':
        if (isClick) {
          this.currencySelectorClicked = !this.currencySelectorClicked;
        }
        if (this.currencySelectorClicked) {
          this.currencySelectorVisible = true;
          break;
        }
        this.currencySelectorVisible = !this.currencySelectorVisible;
        break;
      case 'parametersSelector':
        if (isClick) {
          this.parametersSelectorClicked = !this.parametersSelectorClicked;
        }
        if (this.parametersSelectorClicked) {
          this.parametersSelectorVisible = true;
          break;
        }
        this.parametersSelectorVisible = !this.parametersSelectorVisible;
        break;
    }
    // focus on first opened texteditor
    $('.dx-texteditor-input').first().focus();
  }

  currencySelectionOnChanged(data: any) {
    if (this.currencyDropdownSelected != data) {
      this.currencyDropdownSelected = data;
      this.currencySelectionChanged.emit(data);
    }
  }

  currencyConversionTypeOnChanged(data: any) {
    this.currencyConversionSelectionChanged.emit(
      parseInt(CurrencyConversionTypeEnum[data])
    );
  }

  massUnitSelectionChange(data: any) {
    if (this.unitOfMassDropdownSelected != data) {
      this.unitOfMassDropdownSelected = data;
      this.massUnitSelectionChanged.emit(data);
    }
  }

  exportToExcelSelectionOnChanged(data: any) {
    this.exportTypeDropdownSelected = data;
    switch (data.id) {
      case 1:
        this.exportToExcelSelectorVisible = false;
        this.exportToExcel();
        break;
      case 2:
        this.exportToExcelSelectorVisible = false;
        this.onExportToCvs.emit(true);
        break;
      case 3:
        this.exportToExcelSelectorVisible = false;
        this.onExportSnapshot.emit(true);
        break;
      default:
        break;
    }
  }

  exportToExcel() {
    this.onExportToExcel.emit(true);
  }

  getAutoSaveStateStatus(
    pivotGridOptions: PivotGridOptions,
    userStateAndSetttings: UserStateAndSetttings
  ) {
    if (pivotGridOptions && userStateAndSetttings) {
      if (userStateAndSetttings.settings.rememberLatestState == undefined) {
        return pivotGridOptions.canSaveUserState;
      } else {
        return userStateAndSetttings.settings.rememberLatestState;
      }
    }
  }

  changeAutoSaveStatus(currentStatus: boolean) {
    this.autoSaveStateStatus = !currentStatus;
    this.onChangeAutoSaveStatus.emit(this.autoSaveStateStatus);
  }
}
