import {
  Component,
  Input,
  OnInit,
  SimpleChanges,
  ViewChild,
} from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { AlertService } from '@full-fledged/alerts';
import { IsLoadingService } from '@service-work/is-loading';
import { DxPivotGridComponent } from 'devextreme-angular';
import PivotGridDataSource from 'devextreme/ui/pivot_grid/data_source';
import { marketConsumptionEHCItems } from 'src/model/marketConsumptionEHCItems.model';
import { StudyService } from 'src/services/study.service';
import {
  Field as PivotGridField,
  Format,
  PivotGridOptions,
  PivotGridSource,
} from 'src/model/pivotGridOptions.model';
import { EHCSalesItem } from 'src/model/emptyHardCapsule.model';

@Component({
  selector: 'app-ehc-sales-preview',
  templateUrl: './ehc-sales-preview.component.html',
  styleUrls: ['./ehc-sales-preview.component.css'],
})
export class EhcSalesPreviewComponent implements OnInit {
  @Input() studyData: EHCSalesItem[] = [];
  @Input() currentYear: number = 0;
  @Input() previousYear: number = 0;

  @ViewChild(DxPivotGridComponent, { static: true }) pivotGrid:
    | DxPivotGridComponent
    | undefined;

  previewData: any[] = [];
  saveStateEnabled: boolean = false;
  @Input() set setPreviewData(data: any) {
    if (data) {
      this.previewData = data;
    }
  }

  gridFields: PivotGridField[] = [];
  @Input() set setGridFields(data: any) {
    if (data) {
      this.gridFields = data;
    }
  }

  previewGridKey: string = '';
  @Input() set setPreviewGridKey(data: any) {
    if (data) {
      this.previewGridKey = data;
    }
  }

  marketYearCategoryId: number = 0;
  studyProjectMarketYearId: number = 0;
  studyAreaId: number = 0;

  studyType: string = '';
  @Input() set setStudyType(data: string) {
    if (data) {
      this.studyType = data;
    }
  }

  pivotGridDataSource: PivotGridDataSource;
  pivotGridOptions: PivotGridOptions | undefined;

  isPercentOfTotalPopup = false;
  isPercentOfChangePopup = false;
  isCagrPopup = false;

  yearDataFieldName: string = '';
  isHeaderFilterLoading = false;

  constructor(
    private route: ActivatedRoute,
    private isLoadingService: IsLoadingService,
    private studyService: StudyService,
    private alertService: AlertService
  ) {
    this.pivotGridDataSource = new PivotGridDataSource();

    const params = this.route.snapshot.params;

    this.marketYearCategoryId = params['mycId'];
    this.studyProjectMarketYearId = params['spmId'];
    this.studyAreaId = params['studyArea'];
    this.pivotGridOptions = new PivotGridOptions();
  }

  ngOnInit(): void {
    if (typeof this.pivotGridOptions !== 'undefined') {
      this.pivotGridOptions.pivotGridSource.fields = this.gridFields;
      this.pivotGridOptions.pivotGridSource.store = this.previewData;

      this.pivotGridDataSource = new PivotGridDataSource(
        this.pivotGridOptions.pivotGridSource as any
      );
    }
  }

  ngOnChanges(changes: SimpleChanges): void {
    this.getPreviewData(this.studyData);
  }

  getPreviewData(studyData: EHCSalesItem[]) {
    const previewData: {
      Channel: string;
      Segment: string;
      Sector: string;
      ProductType: string;
      CustomerType: string;
      Polymer: string;
      Supplier: string;
      Functionality: string;
      Year: number;
      CAGR: number;
      Unit: number;
      Sales: number;
      Avg: number;
    }[] = [];

    studyData.map((item: any) => {
      previewData.push({
        Channel: item.Channel,
        Segment: item.Segment,
        Sector: item.Sector,
        CustomerType: item.CustomerType,
        Year: this.previousYear,
        CAGR: item.Cagr1,
        Unit: item.Unit1,
        ProductType: item.ProductType,
        Polymer: item.Polymer,
        Supplier: item.SupplierName,
        Functionality: item.FunctionalityName,
        Sales: item.Value1,
        Avg: item.AvgPrice1,
      });

      previewData.push({
        Channel: item.Channel,
        Segment: item.Segment,
        Sector: item.Sector,
        CustomerType: item.CustomerType,
        Year: this.currentYear,
        CAGR: item.Cagr2,
        Unit: item.Unit2,
        ProductType: item.ProductType,
        Polymer: item.Polymer,
        Supplier: item.Supplier,
        Functionality: item.Functionality,
        Sales: item.Value2,
        Avg: item.AvgPrice2,
      });
    });
    this.previewData = previewData;

    if (typeof this.pivotGridOptions !== 'undefined') {
      this.pivotGridOptions.pivotGridSource.fields = this.getGridFields();
      this.pivotGridOptions.pivotGridSource.store = this.previewData;

      this.pivotGridDataSource = new PivotGridDataSource(
        this.pivotGridOptions.pivotGridSource as any
      );
    }
  }

  getGridFields() {
    return (this.gridFields = [
      {
        dataField: 'Channel',
        area: 'row',
        caption: 'Channel',
        dataType: 'string',
      },
      {
        dataField: 'Segment',
        area: 'row',
        caption: 'Segment',
        dataType: 'string',
      },
      {
        dataField: 'Sector',
        area: 'row',
        caption: 'Sector',
        dataType: 'string',
      },
      {
        dataField: 'ProductType',
        area: 'row',
        caption: 'ProductType',
        dataType: 'string',
      },
      {
        dataField: 'CustomerType',
        area: 'row',
        caption: 'CustomerType',
        dataType: 'string',
      },
      {
        dataField: 'Polymer',
        area: 'row',
        caption: 'Polymer',
        dataType: 'string',
      },
      {
        dataField: 'Supplier',
        area: 'row',
        caption: 'Supplier',
        dataType: 'string',
      },
      {
        dataField: 'Functionality',
        area: 'row',
        caption: 'Functionality',
        dataType: 'string',
      },
      {
        visible: true,
        isCalculated: false,
        calculatedFieldType: 'none',
        id: 6,
        caption: 'Unit',
        dataField: 'Unit',
        dataFieldType: 'Unit',
        width: 80,
        dataType: 'number',
        summaryType: 'sum',
        summaryDisplayMode: 'none',
        format: {
          type: 'fixedPoint',
          precision: 1,
        },
        area: 'data',
      },
      {
        visible: true,
        isCalculated: false,
        calculatedFieldType: 'none',
        id: 6,
        caption: 'Sales',
        dataField: 'Sales',
        dataFieldType: 'Sales',
        width: 80,
        dataType: 'number',
        summaryType: 'sum',
        summaryDisplayMode: 'none',
        format: {
          type: 'fixedPoint',
          precision: 1,
        },
        area: 'data',
      },
      {
        visible: true,
        isCalculated: false,
        calculatedFieldType: 'none',
        id: 6,
        caption: 'Avg. Price',
        dataField: 'Avg',
        dataFieldType: 'Avg',
        width: 80,
        dataType: 'number',
        summaryType: 'sum',
        summaryDisplayMode: 'none',
        format: {
          type: 'fixedPoint',
          precision: 1,
        },
        area: 'data',
      },
      {
        visible: true,
        isCalculated: false,
        calculatedFieldType: 'none',
        id: 6,
        caption: 'CAGR(%)',
        dataField: 'CAGR',
        dataFieldType: 'CAGR',
        width: 80,
        dataType: 'number',
        summaryType: 'sum',
        summaryDisplayMode: 'none',
        format: {
          type: 'fixedPoint',
          precision: 1,
        },
        area: 'data',
      },
      {
        visible: true,
        isCalculated: false,
        calculatedFieldType: 'none',
        id: 7,
        caption: 'Year',
        dataField: 'Year',
        width: 30,
        dataType: 'string',
        summaryDisplayMode: 'none',
        area: 'column',
      },
    ] as any[]);
  }
}
