import {
  Component,
  Input,
  OnChanges,
  OnInit,
  SimpleChanges,
} from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import PivotGridDataSource, {
  Field,
} from 'devextreme/ui/pivot_grid/data_source';
import { SyntheticPenetrationItem } from 'src/model/syntheticPenetration.model';

@Component({
  selector: 'app-synthetic-penetration-preview',
  templateUrl: './synthetic-penetration-preview.component.html',
  styleUrls: ['./synthetic-penetration-preview.component.css'],
})
export class SyntheticPenetrationPreviewComponent implements OnInit, OnChanges {
  @Input() studyData: SyntheticPenetrationItem[] = [];
  @Input() currentYear: number = 0;
  @Input() previousYear: number = 0;

  marketYearCategoryId: number = 0;
  studyProjectMarketYearId: number = 0;
  studyAreaId: number = 0;

  pivotGridDataSource: PivotGridDataSource;
  gridFields: Field[] = [];

  constructor(private route: ActivatedRoute) {
    const params = this.route.snapshot.params;

    this.marketYearCategoryId = params['mycId'];
    this.studyProjectMarketYearId = params['spmId'];
    this.studyAreaId = params['studyArea'];

    this.pivotGridDataSource = new PivotGridDataSource();
  }

  ngOnInit(): void {}

  ngOnChanges(changes: SimpleChanges): void {
    this.getPreviewData(this.studyData);
  }

  getPreviewData(studyData: SyntheticPenetrationItem[]) {
    const previewData: {
      Segment: string;
      SyntheticType: string;
      Year: number;
      Volume: number;
    }[] = [];

    studyData.map((item: SyntheticPenetrationItem) => {
      previewData.push({
        Segment: item.Segment,
        SyntheticType: item.SyntheticType,
        Year: this.previousYear,
        Volume: item.PreviousYear1,
      });
      previewData.push({
        Segment: item.Segment,
        SyntheticType: item.SyntheticType,
        Year: this.currentYear,
        Volume: item.CurrentYear,
      });
    });

    this.pivotGridDataSource = new PivotGridDataSource({
      store: previewData,
      fields: this.getGridFields(),
    });
  }

  getGridFields() {
    return (this.gridFields = [
      {
        dataField: 'Segment',
        area: 'row',
        caption: 'Segment',
        dataType: 'string',
        summaryDisplayMode: 'none',
        width: 200,
      },
      {
        dataField: 'SyntheticType',
        area: 'column',
        caption: 'Synthetic Type',

        dataType: 'string',
      },
      {
        visible: true,
        isCalculated: false,
        calculatedFieldType: 'none',
        id: 6,
        caption: 'Market Penetration',
        dataField: 'Volume',
        dataFieldType: 'Volume',
        width: 30,
        dataType: 'number',
        summaryType: 'sum',
        summaryDisplayMode: 'none',
        format: {
          type: 'fixedPoint',
          precision: 1,
        },
        area: 'data',
      },
      {
        visible: true,
        isCalculated: false,
        calculatedFieldType: 'none',
        id: 7,
        caption: 'Year',
        dataField: 'Year',
        width: 30,
        dataType: 'string',
        summaryDisplayMode: 'none',
        area: 'column',
      },
    ] as Field[]);
  }

  changeStudyData(studyData: SyntheticPenetrationItem[]) {
    this.studyData = studyData;
    this.getPreviewData(this.studyData);
  }
}
