import { Component, Input, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AlertService } from '@full-fledged/alerts';
import { IsLoadingService } from '@service-work/is-loading';
import { Constants } from 'src/app/Constant';
import { ApiResponse } from 'src/model/apiResponse.model';
import { InitialSetup } from 'src/model/initialSetup.model';
import { Market } from 'src/model/market.model';
import { UserDetail } from 'src/model/userDetail.model';
import { LocalStorageService } from 'src/services/localStorage.service';
import { SharedService } from 'src/services/shared.service';

@Component({
  selector: 'app-disable-study-editing',
  templateUrl: './disable-study-editing.component.html',
  styleUrls: ['./disable-study-editing.component.css'],
})
export class DisableStudyEditingComponent implements OnInit {
  isUnlockStudy: boolean = false;
  isLockedByOther: boolean = false;
  isSignOffStudy: boolean = false;

  studyInitialSetup?: InitialSetup;
  @Input() set studyInitialSetupData(data: InitialSetup) {
    if (data) {
      this.studyInitialSetup = data;
      this.isUnlockStudy =
        this.studyInitialSetup.LockedForEditBy === 0 ||
        this.studyInitialSetup.LockedForEditBy === null
          ? true
          : false;

      this.isLockedByOther =
        !this.isUnlockStudy &&
        this.studyInitialSetup.LockedForEditBy !== this.currentUser.Id
          ? true
          : false;

      this.isSignOffStudy = this.studyInitialSetup.IsSignOff || false;
      this.getMarket();
    }
  }

  @Input() marketYearCategoryId = 0;

  isStudyDataFound: boolean = true;
  @Input() set setIsStudyDataFound(data: boolean) {
    this.isStudyDataFound = data;
  }

  currentUser: UserDetail;

  markets: Market[] = [];

  selectedMarket: any;

  constructor(
    private localStorageService: LocalStorageService,
    private sharedService: SharedService,
    private alertService: AlertService,
    private isLoadingService: IsLoadingService,
    private router: Router
  ) {
    this.currentUser = new UserDetail().deserialize(
      this.localStorageService.getData(Constants.userDetailKey)
    );
  }

  ngOnInit(): void {}

  lockStudy() {
    this.isLoadingService.add();
    this.sharedService
      .setLockUnlockStudy(this.marketYearCategoryId, true)
      .subscribe(
        (res: ApiResponse) => {
          if (res.HttpStatusCode === 200) {
            this.alertService.success(res.Data);
            this.reloadCurrentRoute();
          } else {
            this.alertService.danger(res.Data);
          }
          this.isLoadingService.remove();
        },
        (err) => {
          //if (err.status === 401) {
            this.isLoadingService.remove();
          //}
        }
      );
  }

  unlockStudy() {
    this.isLoadingService.add();
    this.sharedService
      .setLockUnlockStudy(this.marketYearCategoryId, false)
      .subscribe(
        (res: ApiResponse) => {
          if (res.HttpStatusCode === 200) {
            this.alertService.success(res.Data);
            this.reloadCurrentRoute();
          } else {
            this.alertService.danger(res.Data);
          }
          this.isLoadingService.remove();
        },
        (err) => {
          //if (err.status === 401) {
            this.isLoadingService.remove();
          //}
        }
      );
  }

  requestForUnlock() {
    this.isLoadingService.add();
    this.sharedService
      .requestForUnlockStudy(this.marketYearCategoryId, this.studyInitialSetup)
      .subscribe(
        (res: ApiResponse) => {
          if (res.HttpStatusCode === 200) {
            this.alertService.success(res.Data);
          } else {
            this.alertService.danger(res.Data);
          }
          this.isLoadingService.remove();
        },
        (err) => {
          //if (err.status === 401) {
            this.isLoadingService.remove();
          //}
        }
      );
  }

  reloadCurrentRoute() {
    let currentUrl = this.router.url;
    this.router.navigateByUrl('/', { skipLocationChange: true }).then(() => {
      this.router.navigate([currentUrl]);
    });
  }

  getMarket() {
    const dataSetId = this.studyInitialSetup?.StudyDataSetId || 0;
    const year = this.studyInitialSetup?.StudyYear || new Date().getFullYear();
    this.sharedService
      .getMarket(dataSetId, year)
      .subscribe((res: ApiResponse) => {
        if (res.HttpStatusCode === 200) {
          this.markets = res.Data as Market[];
        }
      });
  }

  copyStudyData() {
    this.isLoadingService.add();
    this.sharedService
      .migrateDataFromAnotherMarket(
        this.marketYearCategoryId,
        this.selectedMarket.Id
      )
      .subscribe(
        (res: ApiResponse) => {
          if (res.HttpStatusCode === 200) {
            this.alertService.success(res.Data);
            this.reloadCurrentRoute();
          } else {
            this.alertService.danger(res.Data);
          }
          this.isLoadingService.remove();
        },
        (err) => {
          //if (err.status === 401) {
            this.isLoadingService.remove();
          //}
        }
      );
  }
}
