import {
  Component,
  Input,
  OnInit,
  SimpleChanges,
  ViewChild,
} from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AlertService } from '@full-fledged/alerts';
import { IsLoadingService } from '@service-work/is-loading';
import { ApiResponse } from 'src/model/apiResponse.model';
import { StudyService } from 'src/services/study.service';
import { InitialSetup } from 'src/model/initialSetup.model';
import PivotGridDataSource, {
  Field,
} from 'devextreme/ui/pivot_grid/data_source';
import {
  supplierMarketShare,
  SupplierMarketShareItem,
} from 'src/model/supplierMarketShare.model';
import { DataEntry } from 'src/model/dataEntry.model';
import { SharedService } from 'src/services/shared.service';

@Component({
  selector: 'app-supplier-market-share-preview',
  templateUrl: './supplier-market-share-preview.component.html',
  styleUrls: ['./supplier-market-share-preview.component.css'],
})
export class SupplierMarketSharePreviewComponent implements OnInit {
  marketYearCategoryId: number = 0;
  studyProjectMarketYearId: number = 0;
  studyAreaId: number = 0;

  // studyData: SupplierMarketShareItem[] = [];

  studyData: SupplierMarketShareItem[] = [];
  @Input() set setStudyData(v: SupplierMarketShareItem[]) {
    // console.log(v);
    this.studyData = v;
  }

  // get studyData(): SupplierMarketShareItem[] {
  //   return this._studyData;
  // }

  @Input() currentYear: number = 0;
  @Input() previousYear1: number = 0;

  // currentYear: number = 0;
  // previousYear1: number = 0;

  studySummary: InitialSetup = new InitialSetup().deserialize({});

  pivotGridDataSource: PivotGridDataSource;
  gridFields: Field[] = [];

  marketSegments: DataEntry[] = [];

  marketSegmentVisePreviousYearTotal: any = {};
  marketSegmentViseCurrentYearTotal: any = {};

  constructor(
    private studyService: StudyService,
    private isLoadingService: IsLoadingService,
    private alertService: AlertService,
    private route: ActivatedRoute,
    private sharedService: SharedService
  ) {
    const params = this.route.snapshot.params;

    this.marketYearCategoryId = params['mycId'];
    this.studyProjectMarketYearId = params['spmId'];
    this.studyAreaId = params['studyArea'];

    this.pivotGridDataSource = new PivotGridDataSource();
    this.getMarketSegment();
  }

  ngOnInit(): void {
    // this.getSupplierMarketShareData();
  }

  ngOnChanges(changes: SimpleChanges): void {
    //Called before any other lifecycle hook. Use it to inject dependencies, but avoid any serious work here.
    //Add '${implements OnChanges}' to the class.
    // console.log(changes);
    this.getPreviewData(this.studyData);
  }

  changeStudyData(studyData: SupplierMarketShareItem[]) {
    this.setStudyData = studyData;
    this.getPreviewData(this.studyData);
  }

  getPreviewData(studyData: SupplierMarketShareItem[]) {
    const currentYear = this.currentYear;
    // this.foreCastYear1 = currentYear + 4;
    // this.foreCastYear2 = currentYear + 10;
    // this.foreCastYear2 = this.foreCastYear1 + 5;

    this.marketSegments.map((segmentData: DataEntry) => {
      this.marketSegmentVisePreviousYearTotal[segmentData.Name] = this.studyData
        .filter((data) => data.MarketSegmentId === segmentData.Id)
        .reduce((prev: number, curr: any) => prev + curr.PreviousYear1, 0);

      this.marketSegmentViseCurrentYearTotal[segmentData.Name] = this.studyData
        .filter((data) => data.MarketSegmentId === segmentData.Id)
        .reduce((prev: number, curr: any) => prev + curr.CurrentYear, 0);
    });

    const previewData: {
      MarketSegment: string;
      SupplierName: string;
      Year: number;
      Volume: number;
      perOfTotal: number;
    }[] = [];
    studyData.map((item: SupplierMarketShareItem) => {
      let prevPer: number = 0;
      if (item.PreviousYear1 > 0) {
        prevPer = this.calculatePerVolume(
          item.PreviousYear1,
          this.marketSegmentVisePreviousYearTotal[item.MarketSegment]
        );
      }

      let currPer: number = 0;
      if (item.CurrentYear > 0) {
        currPer = this.calculatePerVolume(
          item.CurrentYear,
          this.marketSegmentViseCurrentYearTotal[item.MarketSegment]
        );
      }

      previewData.push({
        MarketSegment: item.MarketSegment,
        SupplierName: item.SupplierName,
        Year: this.previousYear1,
        Volume: item.PreviousYear1,
        perOfTotal: prevPer,
      });
      previewData.push({
        MarketSegment: item.MarketSegment,
        SupplierName: item.SupplierName,
        Year: this.currentYear,
        Volume: item.CurrentYear,
        perOfTotal: currPer,
      });
    });

    this.pivotGridDataSource = new PivotGridDataSource({
      store: previewData,
      fields: this.getGridFields(),
    });
  }

  getSupplierMarketShareData() {
    this.isLoadingService.add();
    this.studyService
      .getSupplierMarketShareData(
        this.marketYearCategoryId,
        this.studyProjectMarketYearId
      )
      .subscribe((res: ApiResponse) => {
        if (res.HttpStatusCode === 200) {
          const studyData = res.Data as supplierMarketShare;

          this.studyData =
            studyData.SupplierMarketShareItems as SupplierMarketShareItem[];

          this.studySummary = new InitialSetup().deserialize(
            studyData.InitialSetup
          );

          let currentYear =
            this.studySummary.StudyYear || new Date().getFullYear();

          currentYear = currentYear - 2;

          this.currentYear = currentYear;
          this.previousYear1 = currentYear - 1;

          this.marketSegments.map((segmentData: DataEntry) => {
            this.marketSegmentVisePreviousYearTotal[segmentData.Name] =
              this.studyData
                .filter((data) => data.MarketSegmentId === segmentData.Id)
                .reduce(
                  (prev: number, curr: any) => prev + curr.PreviousYear1,
                  0
                );

            this.marketSegmentViseCurrentYearTotal[segmentData.Name] =
              this.studyData
                .filter((data) => data.MarketSegmentId === segmentData.Id)
                .reduce(
                  (prev: number, curr: any) => prev + curr.CurrentYear,
                  0
                );
          });

          // const totalPreviousVolume = this.studyData.reduce(
          //   (prev: number, curr: any) => prev + curr.PreviousYear1,
          //   0
          // );
          // const totalCurrentVolume = this.studyData.reduce(
          //   (prev: number, curr: any) => prev + curr.CurrentYear,
          //   0
          // );

          const previewData: {
            MarketSegment: string;
            SupplierName: string;
            Year: number;
            Volume: number;
            perOfTotal: number;
          }[] = [];
          this.studyData.map((item: SupplierMarketShareItem) => {
            let prevPer: number = 0;
            if (item.PreviousYear1 > 0) {
              prevPer = this.calculatePerVolume(
                item.PreviousYear1,
                this.marketSegmentVisePreviousYearTotal[item.MarketSegment]
              );
            }

            let currPer: number = 0;
            if (item.CurrentYear > 0) {
              currPer = this.calculatePerVolume(
                item.CurrentYear,
                this.marketSegmentViseCurrentYearTotal[item.MarketSegment]
              );
            }

            previewData.push({
              MarketSegment: item.MarketSegment,
              SupplierName: item.SupplierName,
              Year: this.previousYear1,
              Volume: item.PreviousYear1,
              perOfTotal: prevPer,
            });
            previewData.push({
              MarketSegment: item.MarketSegment,
              SupplierName: item.SupplierName,
              Year: this.currentYear,
              Volume: item.CurrentYear,
              perOfTotal: currPer,
            });
          });

          this.pivotGridDataSource = new PivotGridDataSource({
            store: previewData,
            fields: this.getGridFields(),
          });

          this.isLoadingService.remove();
        } else {
          this.alertService.danger(res.Data);
        }
      });
  }

  getMarketSegment() {
    this.sharedService.getMarketSegment().subscribe((res: ApiResponse) => {
      if (res.HttpStatusCode === 200) {
        this.marketSegments = res.Data as DataEntry[];
      }
    });
  }

  calculatePerVolume(colValue: number, totalColValue: number) {
    return parseFloat(((colValue * 100) / totalColValue).toFixed(1));
  }

  getGridFields() {
    return (this.gridFields = [
      {
        dataField: 'MarketSegment',
        area: 'column',
        caption: 'Market Segment',
        dataType: 'string',
        summaryDisplayMode: 'none',
      },
      {
        dataField: 'SupplierName',
        area: 'row',
        caption: 'Supplier',
        width: 150,
        dataType: 'string',
      },
      {
        visible: true,
        isCalculated: false,
        calculatedFieldType: 'none',
        id: 6,
        caption: 'Lubricant Demand, KTonnes',
        dataField: 'Volume',
        dataFieldType: 'Volume',
        width: 80,
        dataType: 'number',
        summaryType: 'sum',
        summaryDisplayMode: 'none',
        format: {
          type: 'fixedPoint',
          precision: 1,
        },
        area: 'data',
      },
      {
        visible: true,
        isCalculated: false,
        calculatedFieldType: 'none',
        id: 6,
        caption: '% of Total',
        dataField: 'perOfTotal',
        dataFieldType: 'perOfTotal',
        width: 80,
        dataType: 'number',
        summaryType: 'sum',
        summaryDisplayMode: 'none',
        format: {
          type: 'fixedPoint',
          precision: 1,
        },
        area: 'data',
      },
      {
        visible: true,
        isCalculated: false,
        calculatedFieldType: 'none',
        id: 7,
        caption: 'Year',
        dataField: 'Year',
        width: 80,
        dataType: 'string',
        summaryDisplayMode: 'none',
        area: 'column',
      },
    ] as Field[]);
  }
}
