import { Injectable } from '@angular/core';
import { Constants } from 'src/app/Constant';
import { HttpClient, HttpParams } from '@angular/common/http';
import { throwError, Observable } from 'rxjs';
import { catchError, map } from 'rxjs/operators';
import { ApiResponse } from 'src/model/apiResponse.model';

@Injectable({
  providedIn: 'root',
})
export class UserService {
  apiEndpoint: string = '';
  constructor(private http: HttpClient) {
    this.apiEndpoint = Constants.apiEndpoint;
  }

  getUserDetail(emailAddress: string) {
    const params = JSON.stringify(emailAddress);
    return this.http
      .post<ApiResponse>(`${this.apiEndpoint}/UserDetails/UserDetail`, params, {
        headers: {
          'Content-Type': 'application/json',
        },
      })
      .pipe(
        map((data) => {
          return data;
        })
      );
  }
}
