import { Component, Input, OnInit } from '@angular/core';
import { UserDetail } from 'src/model/userDetail.model';
import { LocalStorageService } from 'src/services/localStorage.service';
import { Constants } from 'src/app/Constant';
import { UserService } from 'src/services/user.service';
import { ApiResponse } from 'src/model/apiResponse.model';
import { StudyAreaSummary } from 'src/model/studyAreaSummary.model';
import { SharedService } from 'src/services/shared.service';
import { AlertService } from '@full-fledged/alerts';
import { IsLoadingService } from '@service-work/is-loading';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css'],
})
export class HeaderComponent implements OnInit {
  title: string = '';
  @Input() set setTitle(title: string) {
    this.title = title;
  }
  studiesData: StudyAreaSummary[] = [];
  userDetail: UserDetail;
  searchExpressionItem = 'SearchExp';
  constantEnv = Constants.environment;

  constructor(
    private localStorageService: LocalStorageService,
    private userService: UserService,
    private sharedService: SharedService,
    private alertService: AlertService,
    private isLoadingService: IsLoadingService,
    private router: Router,
    private route: ActivatedRoute,
  ) {
    this.userDetail = new UserDetail().deserialize(
      this.localStorageService.getData(Constants.userDetailKey)
    );
  }

  ngOnInit(): void {
    this.getUserDetail(this.userDetail.UserName || '');
    this.userDetail = new UserDetail().deserialize(
      this.localStorageService.getData(Constants.userDetailKey)
    );
    this.getAllStudies();
  }

  getUserDetail(emailAddress: string) {
    this.userService.getUserDetail(emailAddress).subscribe(
      (res: ApiResponse) => {
        if (res.HttpStatusCode === 200) {
          const userDetail = new UserDetail().deserialize(res.Data);
          this.localStorageService.setData(userDetail, Constants.userDetailKey);
        }
      },
      (err) => { }
    );
  }
  getAllStudies() {
    this.isLoadingService.add();
    this.sharedService.getStudyAreaSummary().subscribe(
      (res: ApiResponse) => {
        if (res.HttpStatusCode === 200) {
          // res.Data.sort((a,b) => b-a)
          this.studiesData = res.Data.map((item: StudyAreaSummary) =>
            new StudyAreaSummary().deserialize(item)
          );
          this.studiesData.map((item: any) => {
            item.backColour = 'white';
            if (item.IsSignOff) {
              item.backColour = 'lightgray';
            }
            item.SearchExp = item.StudyAreaName +": " +item.StudyName;
          });
          
          this.filterOpenStudy();
          this.studiesData.sort((a, b) => Number(a.LockedForEditBy && a.IsSignOff) - Number(b.LockedForEditBy && b.IsSignOff));
        } else {
          this.alertService.danger(res.Data);
        }
        this.isLoadingService.remove();
      },
      (err) => {
        if (err.status === 401) {
          this.isLoadingService.remove();
        }
      }
    );
  }

  filterOpenStudy(){
    const params = this.route.snapshot.params;
    const marketYearCategoryId = parseInt(params['mycId']);
    const studyProjectMarketYearId = parseInt(params['spmId']);
    const studyAreaId = parseInt(params['studyArea']);
    const dataSetId =parseInt(params['dataSetId']);
    const openStudy = this.studiesData.find(item=> 
      item.StudyAreaId === studyAreaId 
      && item.DataSetId === dataSetId
      && item.MarketYearCategoryId === marketYearCategoryId
      && item.StudyProjectMarketYearId === studyProjectMarketYearId);

    if(openStudy){
      var idx = this.studiesData.indexOf(openStudy);
      if (idx != -1) 
      this.studiesData.splice(idx, 1);
    }
  }
  
  studySelectionChange(event: any) {
    if (event && event.selectedItem) {
      let selectedStudy = event.selectedItem;
      this.isLoadingService.add();
      this.router.navigate(['/study-area',{
        'studyArea': selectedStudy.StudyAreaId,
        'spmId': selectedStudy.StudyProjectMarketYearId,
        'mycId': selectedStudy.MarketYearCategoryId,
        'dataSetId': selectedStudy.DataSetId
      }]);
      const url = 
      `/study-area/${selectedStudy.StudyAreaId}/${selectedStudy.StudyProjectMarketYearId}/${selectedStudy.MarketYearCategoryId}/${selectedStudy.DataSetId}`
      this.loadCurrentUrl(url);
      this.isLoadingService.remove();
    }
  }
  loadCurrentUrl(url:any) {
    this.router.navigateByUrl('/', { skipLocationChange: true }).then(() => {
      this.router.navigate([url]);
    });
  }
}
