import { Injectable } from '@angular/core';
import { HttpHeaders, HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

import { Constants } from 'src/app/Constant';

@Injectable({
  providedIn: 'root',
})
export class CalculatorService {
  apiEndpoint: string = '';
  constructor(private http: HttpClient) {
    this.apiEndpoint = Constants.apiEndpoint;
  }

  getAllInputsByCalculatorId(calculatorId: number): Observable<any[]> {
    return this.http.get<any>(
      this.apiEndpoint +
        '/Calculator/GetAllInputsByCalculatorId/' +
        calculatorId +
        '/'
    );
  }

  getCalculatorDataById(calculatorId: number): Observable<any[]> {
    return this.http.get<any>(
      this.apiEndpoint +
        '/Calculator/getCalculatorDataById/' +
        calculatorId +
        '/'
    );
  }

  getComputedDefaultValues(inputs: any, pageNumber: number): Observable<any[]> {
    let inp = {
      calculatorInputs: inputs,
      pageNumber: pageNumber,
      type: 'precalculated',
    };
    return this.http.post<any>(this.apiEndpoint + '/Calculator/', inp);
  }

  getComponentDataByInputs(
    inputs: any,
    totalVolume: number,
    totalSales: number
  ): Observable<any[]> {
    let inp = {
      calculatorInputs: inputs,
      pageNumber: 0,
      type: 'final',
      totalVolume: totalVolume,
      totalSalesPrice: totalSales,
    };
    return this.http.post<any>(this.apiEndpoint + '/Calculator/', inp);
  }
}
