import { Injectable } from '@angular/core';
import { Constants } from 'src/app/Constant';
import { HttpClient, HttpParams } from '@angular/common/http';
import { throwError, Observable } from 'rxjs';
import { catchError, map } from 'rxjs/operators';
import { ApiResponse } from 'src/model/apiResponse.model';

@Injectable({
  providedIn: 'root',
})
export class SettingsService {
  apiEndpoint: string = '';
  constructor(private http: HttpClient) {
    this.apiEndpoint = Constants.apiEndpoint;
  }

  getDataGridSettings(dataItemCategoryId: number): Observable<ApiResponse> {
    return this.http
      .get<ApiResponse>(`${this.apiEndpoint}/Configuration/GetDataGridSettings?dataItemCategoryId=${dataItemCategoryId}`, {
        headers: {
          'Content-Type': 'application/json',
        },
      });
  }

  getStudyDatasetAndCategories(): Observable<ApiResponse> {
    return this.http
      .get<ApiResponse>(`${this.apiEndpoint}/Configuration/GetStudyDatasetAndCategories`, {
        headers: {
          'Content-Type': 'application/json',
        },
      });
  }
  
  saveDataGridSettings(dataItemCategoryId: number, dataJson: string): Observable<ApiResponse> {
    const body = {
      dataJson,
      dataItemCategoryId
    }
    return this.http
      .put<ApiResponse>(`${this.apiEndpoint}/Configuration/Configuration`, body, {
        headers: {
          'Content-Type': 'application/json',
        },
      });
  }
}
