import { Component, Input, OnInit, SimpleChanges } from '@angular/core';
import PivotGridDataSource, {
  Field,
} from 'devextreme/ui/pivot_grid/data_source';
import { ServiceCategoryConsumptionItem } from 'src/model/serviceCategoryConsumption.model';

@Component({
  selector: 'app-service-category-consumption-preview',
  templateUrl: './service-category-consumption-preview.component.html',
  styleUrls: ['./service-category-consumption-preview.component.css'],
})
export class ServiceCategoryConsumptionPreviewComponent implements OnInit {
  @Input() studyData: ServiceCategoryConsumptionItem[] = [];
  @Input() currentYear: number = 0;
  @Input() previousYear: number = 0;

  // private _studyData: ServiceCategoryConsumptionItem[] = [];
  // @Input() set studyData(v: ServiceCategoryConsumptionItem[]) {
  //   // console.log(v);
  //   this._studyData = v;
  //   this.getPreviewData();
  // }

  // get studyData(): ServiceCategoryConsumptionItem[] {
  //   return this._studyData;
  // }

  pivotGridDataSource: PivotGridDataSource;
  gridFields: Field[] = [];

  constructor() {
    this.pivotGridDataSource = new PivotGridDataSource();
  }

  ngOnInit(): void {}

  ngOnChanges(changes: SimpleChanges): void {
    this.getPreviewData(this.studyData);
  }

  getPreviewData(studyData: ServiceCategoryConsumptionItem[]) {
    const previewData: {
      EngineOilTypeName: string | null;
      SourceTypeCode: string;
      ServiceCategoryCode: string;
      Year: number;
      Volume: number;
      perOfTotal: number;
    }[] = [];

    studyData.map((item: ServiceCategoryConsumptionItem) => {
      let prevPer: number = 0;
      if (item.PreviousYear1 > 0) {
        prevPer = 0;
      }

      let currPer: number = 0;
      if (item.CurrentYear > 0) {
        currPer = 0;
      }

      previewData.push({
        EngineOilTypeName: item.EngineOilTypeName,
        SourceTypeCode: item.SourceTypeCode,
        ServiceCategoryCode: item.ServiceCategoryCode,
        Year: this.previousYear,
        Volume: item.PreviousYear1,
        perOfTotal: parseFloat(prevPer.toFixed(1)),
      });
      previewData.push({
        EngineOilTypeName: item.EngineOilTypeName,
        SourceTypeCode: item.SourceTypeCode,
        ServiceCategoryCode: item.ServiceCategoryCode,
        Year: this.currentYear,
        Volume: item.CurrentYear,
        perOfTotal: parseFloat(currPer.toFixed(1)),
      });
    });

    this.pivotGridDataSource = new PivotGridDataSource({
      store: previewData,
      fields: this.getGridFields(),
    });
  }

  getGridFields() {
    return (this.gridFields = [
      {
        dataField: 'EngineOilTypeName',
        area: 'column',
        caption: 'Engine Oil Type',
        dataType: 'string',
      },
      {
        dataField: 'SourceTypeCode',
        area: 'row',
        caption: 'Source Type',
        dataType: 'string',
        width: 110,
      },
      {
        dataField: 'ServiceCategoryCode',
        area: 'row',
        caption: 'Service Category',
        width: 150,
        dataType: 'string',
      },
      {
        visible: true,
        isCalculated: false,
        calculatedFieldType: 'none',
        id: 6,
        caption: 'Lubricant Demand, KTonnes',
        dataField: 'Volume',
        dataFieldType: 'Volume',
        width: 80,
        dataType: 'number',
        summaryType: 'sum',
        summaryDisplayMode: 'none',
        format: {
          type: 'fixedPoint',
          precision: 1,
        },
        area: 'data',
      },
      {
        visible: true,
        isCalculated: false,
        calculatedFieldType: 'none',
        id: 6,
        caption: '% of Total',
        dataField: 'perOfTotal',
        dataFieldType: 'perOfTotal',
        width: 80,
        dataType: 'number',
        summaryType: 'sum',
        summaryDisplayMode: 'none',
        format: {
          type: 'fixedPoint',
          precision: 1,
        },
        area: 'data',
      },
      {
        visible: true,
        isCalculated: false,
        calculatedFieldType: 'none',
        id: 7,
        caption: 'Year',
        dataField: 'Year',
        width: 30,
        dataType: 'string',
        summaryDisplayMode: 'none',
        area: 'column',
      },
    ] as Field[]);
  }
}
