import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { IsLoadingService } from '@service-work/is-loading';
import { ApiResponse } from 'src/model/apiResponse.model';
import { StudyAreaCategory } from 'src/model/studyDataSet.model';
import { PendingChangesService } from 'src/services/pending-changes.service';
import { SharedService } from 'src/services/shared.service';

@Component({
  selector: 'app-study-area',
  templateUrl: './study-area.component.html',
  styleUrls: ['./study-area.component.css'],
})
export class StudyAreaComponent implements OnInit {
  marketYearCategoryId: number = 0;
  studyProjectMarketYearId: number = 0;
  studyAreaId: number = 0;
  dataSetId: number = 0;
  @Output() checkPendingChanges = new EventEmitter<any>();
  studyPageTitle: string = '';
  
  constructor(
    private route: ActivatedRoute,
    private sharedService: SharedService,
    private isLoadingService: IsLoadingService,
    private pendingChangesService: PendingChangesService,
  ) {
    const params = this.route.snapshot.params;

    this.marketYearCategoryId = params['mycId'];
    this.studyProjectMarketYearId = params['spmId'];
    this.studyAreaId = params['studyArea'];
    this.dataSetId = params['dataSetId'];

    this.getStudyAreaCategory();
  }

  ngOnInit(): void {
    if (this.isLoadingService.isLoading$()) {
      this.isLoadingService.remove();
    }
  }
  canDeactivate(){
   const canNavigate = this.pendingChangesService.getTableDirtyFlag();
   return !canNavigate; 
  }
  getStudyAreaCategory() {
    this.sharedService
      .getStudyAreaCategory(this.dataSetId)
      .subscribe((res: ApiResponse) => {
        if (res.HttpStatusCode === 200) {
          const studyAreaCategories = res.Data as StudyAreaCategory[];
          const found = studyAreaCategories.filter(
            (item) => item.Id == this.studyAreaId
          );

          if (found) {
            this.studyPageTitle = found[0].Name || '';
          }
        }
      });
  }
}
