import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AuthGuard } from 'src/guard/auth.guard';
import { PendingChangesGuard } from 'src/services/pending-changes.guard';
import { CalculatorComponent } from './calculator/calculator.component';
import { ShareComponent } from './share/share.component';
import { StudyAreaPreviewComponent } from './study-area-preview/study-area-preview.component';
import { StudyAreaSummaryComponent } from './study-area-summary/study-area-summary.component';
import { StudyAreaComponent } from './study-area/study-area.component';

const routes: Routes = [
  {
    path: 'study-summary',
    component: StudyAreaSummaryComponent,
    canActivate: [AuthGuard],
  },
  {
    path: 'study-area/:studyArea/:spmId/:mycId/:dataSetId',
    component: StudyAreaComponent,
    canActivate: [AuthGuard],
    canDeactivate: [PendingChangesGuard]
  },
  {
    path: 'study-area-preview/:spmId/:mycId',
    component: StudyAreaPreviewComponent,
    canActivate: [AuthGuard],
  },
  {
    path: 'calculator/:calculatorId/:marketYearCategoryId/:studyProjectMarketYearId',
    component: CalculatorComponent,
    canActivate: [AuthGuard],
  },
  {
    path: 'share',
    component: ShareComponent,
    canActivate: [AuthGuard],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class PagesRoutingModule {}
