import { Component, Input, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { DxDataGridComponent } from 'devextreme-angular';
import { InitialSetup } from 'src/model/initialSetup.model';
import { UserDetail } from 'src/model/userDetail.model';
import { ActivatedRoute, Router } from '@angular/router';
import { StudyService } from 'src/services/study.service';
import { LocalStorageService } from 'src/services/localStorage.service';
import { Constants } from 'src/app/Constant';
import { ApiResponse } from 'src/model/apiResponse.model';
import { MarketConsumptionEHC } from 'src/model/marketConsumption.model';
import { marketConsumptionEHCItems } from 'src/model/marketConsumptionEHCItems.model';
import { IsLoadingService } from '@service-work/is-loading';
import { AlertService } from '@full-fledged/alerts';
import { SharedService } from 'src/services/shared.service';
import { DataEntry } from 'src/model/dataEntry.model';
import { DataRelationsData } from 'src/model/dataRelationsData.model';
import { DataRelationsEnum } from 'src/model/enums/dataRelations.enum';
import { StudyDataUrlEnum } from 'src/model/enums/studyDataUrl.enum';
import { DisableStudyEditingComponent } from 'src/app/shared/disable-study-editing/disable-study-editing.component';

@Component({
  selector: 'app-market-consumption',
  templateUrl: './market-consumption.component.html',
  styleUrls: ['./market-consumption.component.css'],
})
export class MarketConsumptionComponent implements OnInit {
  @ViewChild(DxDataGridComponent, { static: false })
  dataGrid: DxDataGridComponent | undefined;

  studyTitle: string = '';

  currentYear: number = 0;
  latestDataYear: number = 0;

  marketYearCategoryId: number = 0;
  studyProjectMarketYearId: number = 0;
  studyAreaId: number = 0;

  studySummary: InitialSetup = new InitialSetup().deserialize({});

  popupVisible: boolean = false;
  notePopupVisible: boolean = false;
  confirmPopup: boolean = false;
  viewNotePopup: boolean = false;

  newRowForm: FormGroup;
  newNoteForm: FormGroup;
  noteSubmit = false;
  selectedRowNotes: any;

  datRelationsData: DataRelationsData[] = [];
  @Input() set setDataRelationsData(data: DataRelationsData[]) {
    if (data) {
      this.datRelationsData = data;

      this.studyDataRelations = this.datRelationsData.find(
        (item) =>
          item.DataItemCategoryId === parseInt(this.studyAreaId.toString())
      );

      if (this.studyDataRelations) {
        this.studyDataViewId = this.studyDataRelations.ViewId;
      }
    }
  }
  dataRelationsEnum = DataRelationsEnum;
  dataRelationsForEntry: DataRelationsData[] = [];

  studyDataRelations: DataRelationsData | undefined;
  studyDataViewId: number = 0;
  studyDataUrlEnum = StudyDataUrlEnum;

  selectedRowItem = [];
  isManage: boolean = false;
  isMidYear: boolean = false;
  userDetail: UserDetail;

  studyDataObj: MarketConsumptionEHC = {
    MarketYearCategoryId: 0,
    InitialSetup: new InitialSetup(),
    MarketConsumptionEHCItems: [],
  };

  studyMarketYearCategoryId: number = 0;
  studyData: marketConsumptionEHCItems[] = [];

  disabledSignOffButton: boolean = false;
  isStudyEditable: boolean = false;

  TooltipTarget: any;
  isTooltipVisible: boolean = false;
  TooltipText: string = '';

  customerTypeLists: DataEntry[] = [];
  sectorLists: DataEntry[] = [];
  dosageFormLists: DataEntry[] = [];
  segmentLists: DataEntry[] = [];

  isCagr2Required: Boolean = false;

  currentCurrency: any = {};
  previewGridData: any[] = [];
  previewGridStorageKey: string = '';
  volumeYears: any[] = [];

  deleteRowConfirmPopup: boolean = false;

  isLockedStudy: boolean = false;
  @ViewChild(DisableStudyEditingComponent) disableStudyComponent:
    | DisableStudyEditingComponent
    | undefined;

  constructor(
    private route: ActivatedRoute,
    private studyService: StudyService,
    private isLoadingService: IsLoadingService,
    private formBuilder: FormBuilder,
    private alertService: AlertService,
    private localStorageService: LocalStorageService,
    private sharedService: SharedService,
    private router: Router
  ) {
    this.currentYear = new Date().getFullYear();

    const params = this.route.snapshot.params;

    this.marketYearCategoryId = params['mycId'];
    this.studyProjectMarketYearId = params['spmId'];
    this.studyAreaId = params['studyArea'];

    this.previewGridStorageKey = `EHC-MarketConsumption-${this.studyAreaId}`;

    this.newRowForm = this.formBuilder.group({
      CustomerType: [null, Validators.required],
      Sector: [null, Validators.required],
      Segment: [null, Validators.required],
      DosageForm: [null, Validators.required],
      Unit2: [null, Validators.required],
      Cagr2: [null, Validators.required],
    });

    this.newNoteForm = this.formBuilder.group({
      note: [null, Validators.required],
      rowData: [null],
    });

    this.userDetail = new UserDetail().deserialize(
      this.localStorageService.getData(Constants.userDetailKey)
    );

    this.isManage = this.userDetail.IsManager == true ? true : false;
  }

  ngOnInit(): void {
    this.getConsumptionMarketData();

    this.getCustomerTypeLists();
    this.getSectorLists();
    this.getSegmentLists();
    this.getDosageFormLists();
  }

  getCustomerTypeLists() {
    this.sharedService
      .getDataEntryDropDowns(33)
      .subscribe((res: ApiResponse) => {
        if (res.HttpStatusCode === 200) {
          this.customerTypeLists = res.Data;
        }
      });
  }
  getSectorLists() {
    this.sharedService
      .getDataEntryDropDowns(35)
      .subscribe((res: ApiResponse) => {
        if (res.HttpStatusCode === 200) {
          this.sectorLists = res.Data;
        }
      });
  }
  getDosageFormLists() {
    this.sharedService
      .getDataEntryDropDowns(38)
      .subscribe((res: ApiResponse) => {
        if (res.HttpStatusCode === 200) {
          this.dosageFormLists = res.Data;
        }
      });
  }
  getSegmentLists() {
    this.sharedService
      .getDataEntryDropDowns(37)
      .subscribe((res: ApiResponse) => {
        if (res.HttpStatusCode === 200) {
          this.segmentLists = res.Data;
        }
      });
  }

  getConsumptionMarketData() {
    this.isLoadingService.add();
    this.studyService
      .getMarketConsumptionStudyData(
        this.marketYearCategoryId,
        this.studyProjectMarketYearId
      )
      .subscribe((res: ApiResponse) => {
        if (res.HttpStatusCode === 200) {
          this.studyDataObj = res.Data as MarketConsumptionEHC;

          this.studyMarketYearCategoryId =
            this.studyDataObj.MarketYearCategoryId;

          this.studyData = this.studyDataObj
            .MarketConsumptionEHCItems as marketConsumptionEHCItems[];

          this.studySummary = new InitialSetup().deserialize(
            this.studyDataObj.InitialSetup
          );

          this.isManage =
            this.userDetail.Id === this.studySummary.ProjectLeadId
              ? true
              : false;

          this.isLockedStudy =
            !this.studySummary.IsSignOff &&
            this.studySummary.LockedForEditBy === this.userDetail.Id
              ? true
              : false;

          this.studyTitle = `${this.studySummary.StudyDataSetName}-${this.studySummary.StudyYear}-${this.studySummary.MarketName}`;

          this.isStudyEditable = this.studySummary.IsSignOff || false;

          let currentYear =
            this.studySummary.StudyYear || new Date().getFullYear();

          this.currentYear = currentYear;
          this.latestDataYear =
            typeof this.studyData[0] !== 'undefined'
              ? this.studyData[0].LatestDataYear
              : 0;

          const isDataRowNull = this.studyData.find(
            (data) => data.DataRowId !== null
          );

          if (!isDataRowNull) {
            this.disabledSignOffButton = true;
          }

          this.studyData.map((item: any, i) => {
            const rowNoteArray: any = [];
            if (item.Notes !== null) {
              const noteArray = item.Notes.split('|||');
              noteArray.map((singleVal: any) => {
                const n = singleVal.split('@@@');
                const d = n[0].split('###');
                rowNoteArray.push({
                  name: d[0].trim(),
                  date: new Date(d[1].trim()),
                  note: n[1].trim(),
                });
              });
            }

            let sortRowNoteArray: any = [];
            if (rowNoteArray.length > 0) {
              sortRowNoteArray = rowNoteArray.sort(
                (a: any, b: any) => b.date - a.date
              );
            }

            let changelogObj: any[] = [];
            if (item.ChangeLog !== null) {
              const colValues = item.ChangeLog.split('|||');
              if (colValues) {
                colValues.map((cv: any) => {
                  const colValueArray = cv.trim().split('###');
                  changelogObj[colValueArray[4]] = {};

                  changelogObj[colValueArray[4]] = {
                    name: colValueArray[0],
                    date: colValueArray[1],
                    oldValue: colValueArray[2],
                    year: colValueArray[3],
                  };
                });
              }
            }

            this.previewGridData.push({
              CustomerType: item.CustomerType,
              Sector: item.Sector,
              Segment: item.Segment,
              DosageForm: item.DosageForm,
              Year: this.latestDataYear,
              Unit: item.Unit1,
              CagrPer: item.Cagr1,
            });

            this.previewGridData.push({
              CustomerType: item.CustomerType,
              Sector: item.Sector,
              Segment: item.Segment,
              DosageForm: item.DosageForm,
              Year: this.currentYear,
              Unit: item.Unit2,
              CagrPer: item.Cagr2,
            });

            this.studyData[i].StudyNotes =
              rowNoteArray.length > 0 ? rowNoteArray[0].note : '';
            this.studyData[i].EditNote = false;
            this.studyData[i].Notes = null;
            this.studyData[i].NoteArray = rowNoteArray;
            this.studyData[i].ChangeLogObj = changelogObj;
          });

          this.volumeYears.push({
            field: 'Value1',
            unitField: 'Unit1',
            avgPriceField: 'AvgPrice1',
            cagrField: 'Cagr1',
            year: this.latestDataYear,
          });
          this.volumeYears.push({
            field: 'Value2',
            unitField: 'Unit2',
            avgPriceField: 'AvgPrice2',
            cagrField: 'Cagr2',
            year: this.currentYear,
          });

          this.isLoadingService.remove();
        } else {
          this.alertService.danger(res.Data);
        }
      });
  }

  getGridFields() {
    return [
      {
        dataField: 'CustomerType',
        area: 'row',
        caption: 'Customer Type',
        dataType: 'string',
        id: 0,
      },
      {
        dataField: 'Sector',
        area: 'row',
        caption: 'Sector',
        dataType: 'string',
        id: 1,
      },
      {
        dataField: 'Segment',
        area: 'row',
        caption: 'Segment',
        dataType: 'string',
        id: 2,
      },
      {
        dataField: 'DosageForm',
        area: 'row',
        caption: 'Dosage Form',
        dataType: 'string',
        id: 3,
      },
      {
        visible: true,
        isCalculated: false,
        calculatedFieldType: 'none',
        id: 4,
        caption: 'Unit',
        dataField: 'Unit',
        dataFieldType: 'Unit',
        width: 80,
        dataType: 'number',
        summaryType: 'sum',
        summaryDisplayMode: 'none',
        format: {
          type: 'fixedPoint',
          precision: 1,
        },
        area: 'data',
      },
      {
        visible: true,
        isCalculated: false,
        calculatedFieldType: 'none',
        id: 5,
        caption: 'CAGR(%)',
        dataField: 'CagrPer',
        dataFieldType: 'CagrPer',
        width: 80,
        dataType: 'number',
        summaryType: 'sum',
        summaryDisplayMode: 'none',
        format: {
          type: 'fixedPoint',
          precision: 1,
        },
        area: 'data',
      },
      {
        visible: true,
        isCalculated: false,
        calculatedFieldType: 'none',
        id: 6,
        caption: 'Year',
        dataField: 'Year',
        width: 30,
        dataType: 'string',
        summaryDisplayMode: 'none',
        area: 'column',
      },
    ] as any[];
  }

  addNewRowPopup() {
    this.popupVisible = true;
  }
  get n() {
    return this.newNoteForm.controls;
  }

  submitNewRow() {
    const newRowData = this.newRowForm.value;

    const newRow: marketConsumptionEHCItems = {
      CurrentYear: +newRowData.CurrentYear,
      DataRowId: '',
      Notes: null,
      StudyNotes: '',
      EditNote: false,
      NoteArray: null,
      ChangeLog: '',
      ChangeLogObj: [],
      Cagr1: 0,
      CustomerType: newRowData.CustomerType.Name,
      CustomerTypeId: +newRowData.CustomerType.Id,
      DosageForm: newRowData.DosageForm.Name,
      DosageFormId: +newRowData.DosageForm.Id,
      MarketId: +newRowData.MarketId,
      Sector: newRowData.Sector.Name,
      SectorId: +newRowData.Sector.Id,
      Segment: newRowData.Segment.Name,
      SegmentId: +newRowData.Segment.Id,
      Cagr2: +newRowData.Cagr2,
      LatestDataYear: this.latestDataYear,
      Unit1: 0,
      Unit2: +newRowData.Unit2,
      IsCagr2Required: false,
      IsNewRow: true,
    };

    const isFound = this.studyData.find(
      (item) =>
        item.DosageFormId === newRow.DosageFormId &&
        item.CustomerTypeId === newRow.CustomerTypeId &&
        item.SectorId === newRow.SectorId &&
        item.SegmentId === newRow.SegmentId
    );
    if (!isFound) {
      this.studyData.push(newRow);
      this.studyData.sort(
        (a, b) =>
          (a.DosageFormId || 0) - (b.DosageFormId || 0) ||
          (a.CustomerTypeId || 0) - (b.CustomerTypeId || 0) ||
          (a.SectorId || 0) - (b.SectorId || 0) ||
          (a.SegmentId || 0) - (b.SegmentId || 0)
      );
      this.popupVisible = false;
      this.newRowForm.reset();
      this.newRowForm.updateValueAndValidity();
    } else {
      this.alertService.danger('You have already added this data in row.');
    }
  }

  onRowPrepared(e: any) {
    if (typeof e.rowType !== 'undefined' && e.rowType === 'data') {
      if (typeof e.data.IsNewRow !== 'undefined' && e.data.IsNewRow) {
        e.rowElement.classList.add('highlighter-tr');
      }
    }
  }

  onRowSaved(e: any) {
    if (e.changes) {
      const data = e.changes[0].data as marketConsumptionEHCItems;
      data.Unit2 = +data.Unit2;
      data.Cagr2 = +data.Cagr2;
      if (
        (data.Unit2 != 0 || data.Unit2 != null) &&
        (data.Cagr2 === 0 || data.Cagr2 === null)
      )
        data.IsCagr2Required = true;
      else {
        data.IsCagr2Required = false;
      }
    }
  }
  onCellPreparedHighlighter(e: any) {
    const allowedCols = ['Unit2', 'Cagr2'];

    const isFound = allowedCols.some((item) => item === e.column.dataField);
    if (e.rowType === 'data' && isFound) {
      if (e.data.ChangeLogObj) {
        const cellValue =
          typeof e.data.ChangeLogObj[e.column.dataField] !== 'undefined'
            ? e.data.ChangeLogObj[e.column.dataField]
            : null;
        if (cellValue !== null) {
          e.cellElement.className = 'highlighter-td';
        }
      }
    }
  }

  onCellHoverChanged(event: any) {
    const allowedCols = ['Unit2', 'Cagr2'];

    const isFound = allowedCols.some((item) => item === event.column.dataField);
    if (event.rowType === 'data' && isFound) {
      this.TooltipTarget = event.cellElement;
      if (event.eventType === 'mouseover') {
        if (event.data.ChangeLogObj) {
          const cellValue =
            typeof event.data.ChangeLogObj[event.column.dataField] !==
            'undefined'
              ? event.data.ChangeLogObj[event.column.dataField]
              : null;
          if (cellValue !== null) {
            const oldValue = !isNaN(cellValue.oldValue)
              ? +cellValue.oldValue
              : 0;
            const html = `<div class="row text-left">
                            <div class="col-md-4">Name:</div>
                            <div class="col-md-8">${cellValue.name}</div>
                        </div>
                        <div class="row text-left">
                            <div class="col-md-4">Old Value:</div>
                            <div class="col-md-8">${oldValue.toFixed(1)}</div>
                        </div>
                        <div class="row text-left">
                            <div class="col-md-4">Date:</div>
                            <div class="col-md-8">${cellValue.date}</div>
                        </div>`;
            this.TooltipText = html;
            this.isTooltipVisible = true;
          }
        }
      }

      if (event.eventType === 'mouseout') {
        this.isTooltipVisible = false;
      }
    }
  }

  saveStudyData() {
    const studyData = this.studyData.map((item: any) => ({
      ...item,
      Unit2: item.Unit2 > 0 ? item.Unit2 : 0,
      Cagr2: item.Cagr2 > 0 ? item.Cagr2 : 0,
      Unit1: item.Unit1 > 0 ? item.Unit1 : 0,
      Cagr1: item.Cagr1 > 0 ? item.Cagr1 : 0,
    }));

    const studyDataInfo = {
      marketYearCategoryId: +this.marketYearCategoryId,
      initialSetup: this.studySummary,
      marketConsumptionEHCItems: studyData,
    };

    this.isLoadingService.add();
    this.studyService.saveMaketConsumptionData(studyDataInfo).subscribe(
      (res: ApiResponse) => {
        if (res.HttpStatusCode === 200) {
          this.alertService.success(res.Data);
          this.isLoadingService.remove();
          // this.getConsumptionMarketData();
          this.reloadCurrentRoute();
        } else {
          this.alertService.danger(res.Data);
          this.isLoadingService.remove();
        }
      },
      (err) => {
        this.alertService.danger(
          'Something went wrong. Please try after sometimes.'
        );
        this.isLoadingService.remove();
      }
    );
  }

  onRequestForSignOffClick() {
    this.isLoadingService.add();
    this.studyService
      .requestForSignOffData(this.studySummary)
      .subscribe((res: ApiResponse) => {
        if (res.HttpStatusCode === 200) {
          this.alertService.success(res.Data);
          this.isLoadingService.remove();
        } else {
          this.alertService.danger(res.Data);
          this.isLoadingService.remove();
        }
      });
  }

  onRequestForChangeDataClick() {
    this.isLoadingService.add();
    this.studyService
      .requestForChangeData(this.studySummary)
      .subscribe((res: ApiResponse) => {
        if (res.HttpStatusCode === 200) {
          this.alertService.success(res.Data);
          this.isLoadingService.remove();
        } else {
          this.alertService.danger(res.Data);
          this.isLoadingService.remove();
        }
      });
  }

  onSignOffClick() {
    this.confirmPopup = true;
  }

  onSignOffData() {
    const formData = {
      marketYearCategoryId: +this.marketYearCategoryId,
      initialSetup: this.studySummary,
      marketConsumptionEHCItems: [],
    };

    this.confirmPopup = false;
    this.isLoadingService.add();
    this.studyService.signOffMaketConsumptionData(formData).subscribe(
      (res: ApiResponse) => {
        if (res.HttpStatusCode === 200) {
          this.alertService.success(res.Data);
          this.isLoadingService.remove();
          this.getConsumptionMarketData();
        } else {
          this.alertService.danger(res.Data);
          this.isLoadingService.remove();
        }
      },
      (err) => {
        const error =
          typeof err.error !== 'undefined'
            ? err.error
            : 'Something went wrong. Please try after sometimes.';
        this.alertService.danger(error);
        this.isLoadingService.remove();
      }
    );
  }

  addNotes(data: any) {
    this.newNoteForm.get('note')?.setValue(data.data.Notes);
    this.newNoteForm.get('rowData')?.setValue(data);
    this.notePopupVisible = true;
  }

  showNotePopup(row: any) {
    this.selectedRowNotes = row.data.NoteArray;
    this.viewNotePopup = true;
  }

  onNotePopupHide() {
    this.selectedRowItem = [];
    this.noteSubmit = false;
    this.newNoteForm.reset();
    this.newRowForm.updateValueAndValidity();
    this.selectedRowNotes = [];
    this.viewNotePopup = false;
  }

  onSaveNote() {
    this.noteSubmit = true;
    if (this.newNoteForm.invalid) {
      return;
    }

    const rowData = this.newNoteForm.value.rowData;
    const rowIndex = rowData.rowIndex;
    const newNote = this.newNoteForm.value.note;
    const IsEditedNote = this.dataGrid?.instance.cellValue(
      rowIndex,
      'EditNote'
    );

    this.notePopupVisible = false;
    let noteValue = rowData.data.StudyNotes;
    noteValue = noteValue !== null ? noteValue : '';

    if (IsEditedNote) {
      const noteArray =
        noteValue.indexOf(',') > -1 ? noteValue.split(',') : [noteValue];

      noteArray.shift();

      noteValue = noteArray.join(',');
    }

    const newNoteValue =
      noteValue !== '' ? `${newNote}, ${noteValue}` : newNote;
    this.dataGrid?.instance.cellValue(rowIndex, 'Notes', newNote);
    this.dataGrid?.instance.saveEditData();
    this.dataGrid?.instance.cellValue(rowIndex, 'StudyNotes', newNoteValue);
    this.dataGrid?.instance.saveEditData();
    this.dataGrid?.instance.cellValue(rowIndex, 'EditNote', true);
    this.dataGrid?.instance.saveEditData();
  }

  createNewDataEntry(type: string, newValue: string) {
    const dataRelationData = this.datRelationsData.find(
      (item) =>
        (<any>Object).values(DataRelationsEnum).includes(item.TableName) &&
        item.TableName === type
    );

    if (dataRelationData) {
      this.isLoadingService.add();
      this.sharedService
        .saveNewDataEntry(dataRelationData.DataRelationId, newValue)
        .subscribe(
          (res: ApiResponse) => {
            if (res.HttpStatusCode === 200) {
              this.alertService.success(res.Data);
              this.loadDataEntry(type);
              this.isLoadingService.remove();
            } else {
              this.alertService.danger(res.Data);
              this.isLoadingService.remove();
            }
          },
          (err) => {
            this.isLoadingService.remove();
          }
        );
    }
  }

  loadDataEntry(type: string) {
    switch (type) {
      case DataRelationsEnum.CustomerType:
        this.getCustomerTypeLists();
        break;

      case DataRelationsEnum.Segment:
        this.getSegmentLists();
        break;

      case DataRelationsEnum.DosageForm:
        this.getDosageFormLists();
        break;

      case DataRelationsEnum.Sector:
        this.getSectorLists();
        break;

      default:
        break;
    }
  }

  reloadCurrentRoute() {
    let currentUrl = this.router.url;
    this.router.navigateByUrl('/', { skipLocationChange: true }).then(() => {
      this.router.navigate([currentUrl]);
    });
  }

  deleteRowData: any;
  deleteRow(dataRowId: string) {
    this.deleteRowData = {
      DataSetId: this.studySummary.StudyDataSetId,
      DataItemCategoryId: this.studySummary.StudyAreaId,
      MarketYearCategoryDataId: this.marketYearCategoryId,
      DataRowId: dataRowId,
    };

    this.deleteRowConfirmPopup = true;
  }

  confirmDeleteRow() {
    this.isLoadingService.add();
    this.sharedService
      .removeStudyDataRow(
        this.deleteRowData.DataSetId,
        this.deleteRowData.DataItemCategoryId,
        this.deleteRowData.MarketYearCategoryDataId,
        this.deleteRowData.DataRowId
      )
      .subscribe(
        (res: ApiResponse) => {
          this.onDeleteRowConfirmPopupHide();
          if (res.Data) {
            this.alertService.success(
              'Your data row has been removed successfully.'
            );
            this.reloadCurrentRoute();
          } else {
            this.alertService.danger(
              'Error: Something went wrong. Data row can not removed.'
            );
          }
          this.isLoadingService.remove();
        },
        (err) => {
          const msg =
            typeof err.error !== 'undefined' && err.error !== ''
              ? err.error
              : 'Error: Something went wrong.';

          this.onDeleteRowConfirmPopupHide();
          this.alertService.danger(msg);
          this.isLoadingService.remove();
        }
      );
  }

  onDeleteRowConfirmPopupHide() {
    this.deleteRowData = {};
    this.deleteRowConfirmPopup = false;
  }

  unlockStudy() {
    this.disableStudyComponent?.unlockStudy();
  }
}
