import { Component, OnInit, ViewChild, ViewChildren, NgZone } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AlertService } from '@full-fledged/alerts';
import { IsLoadingService } from '@service-work/is-loading';
import { DxDropDownButtonComponent } from 'devextreme-angular';
import { DisableStudyEditingComponent } from 'src/app/shared/disable-study-editing/disable-study-editing.component';
import { HandsonTableComponent } from 'src/app/shared/handson-table/handson-table.component';
import { ApiResponse } from 'src/model/apiResponse.model';
import { DataRelationsData } from 'src/model/dataRelationsData.model';
import { DataSourceResponse } from 'src/model/dataSourceResponse.model';
import { DataRelationsEnum } from 'src/model/enums/dataRelations.enum';
import { InitialSetup } from 'src/model/initialSetup.model';
import { StudyAreaSummary } from 'src/model/studyAreaSummary.model';
import { SharedService } from 'src/services/shared.service';
import { StudyService } from 'src/services/study.service';
import { DropdownCurrency } from '../../../../../model/dropdownCurrency.model';
import { DropdownUnits } from '../../../../../model/dropdownUnits.model';

@Component({
  selector: 'app-study',
  templateUrl: './study.component.html',
  styleUrls: ['./study.component.css'],
})
export class StudyComponent implements OnInit {
  studyData: any = {};
  studyDataPivotGridPreview: any = {};

  marketYearCategoryId: number = 0;
  studyProjectMarketYearId: number = 0;
  studyAreaId: number = 0;
  dataSetId: number = 0;
  dataRelationsData: DataRelationsData | undefined;
  dataRelationsEnum = DataRelationsEnum;
  studyDataViewId: number = 0;
  lookupDataRelations: any;
  studySummary: InitialSetup = new InitialSetup().deserialize({});

  viewNotePopup: boolean = false;
  selectedCommentCellData: any = {};
  hasStudyData: boolean = false;
  errorOccured: boolean = false;
  summaryData: StudyAreaSummary[] = [];
  deleteRowConfirmPopup: boolean = false;
  deleteRowData: any;

  signOffConfirmPopup: boolean = false;
  signOffData: any;

  currencyDropdown: DropdownCurrency[] = [];
  massUnitDropdown: DropdownUnits[] = [];

  currenCurrency: DropdownCurrency = new DropdownCurrency();
  currentVolumeUnit: DropdownUnits = new DropdownUnits();

  @ViewChildren('studyDropDown') studyDropDown: DxDropDownButtonComponent;
  @ViewChild(DisableStudyEditingComponent) disableStudyComponent:
    | DisableStudyEditingComponent
    | undefined;

  @ViewChild(HandsonTableComponent)
  HandsonTableComponent: HandsonTableComponent;

  constructor(
    private isLoadingService: IsLoadingService,
    private alertService: AlertService,
    private route: ActivatedRoute,
    private sharedService: SharedService,
    private studyService: StudyService,
    private router: Router,
    private ngZone: NgZone
  ) {
    const params = this.route.snapshot.params;

    this.marketYearCategoryId = params['mycId'];
    this.studyProjectMarketYearId = params['spmId'];
    this.studyAreaId = params['studyArea'];
    this.dataSetId = params['dataSetId'];

    this.getDataRelations();
  }

  ngOnInit(): void {
    this.getStudyData(this.marketYearCategoryId, this.studyProjectMarketYearId);
    this.getAllStudies();
    this.getCurrencyList(this.dataSetId);
    this.getUnitOfMassList(this.dataSetId);
  }

  getAllStudies() {
    this.sharedService.getStudyAreaSummary().subscribe(
      (res: ApiResponse) => {
        if (res.HttpStatusCode === 200) {
          // res.Data.sort((a,b) => b-a)
          this.summaryData = res.Data.map((item: StudyAreaSummary) =>
            new StudyAreaSummary().deserialize(item)
          );
          this.summaryData.map((item: any) => {
            item.backColour = 'white';
            if (item.IsSignOff) {
              item.backColour = 'lightgray';
            }
          });
          this.summaryData.sort(
            (a, b) => Number(b.LockedForEditBy) - Number(a.LockedForEditBy)
          );
          this.summaryData.sort(
            (a, b) => Number(a.IsSignOff) - Number(b.IsSignOff)
          );
        } else {
          this.alertService.danger(res.Data);
        }
        this.isLoadingService.remove();
      },
      (err) => {
        // if (err.status === 401) {
        //   this.isLoadingService.remove();
        // }
        this.isLoadingService.remove();
      }
    );
  }
  getDataRelations() {
    this.sharedService.getDataRelationsData().subscribe((res: ApiResponse) => {
      if (res.HttpStatusCode === 200) {
        const data = res.Data as DataRelationsData[];
        this.dataRelationsData = data.find(
          (item) =>
            item.DatabaseId === +this.dataSetId &&
            item.DataItemCategoryId === parseInt(this.studyAreaId.toString())
        );

        if (this.dataRelationsData) {
          this.studyDataViewId = this.dataRelationsData.ViewId;
        }
        this.lookupDataRelations = data.filter(
          (item) => item.DatabaseId === +this.dataSetId
        );
      }
    });
  }

  // Call get api for fetch data
  getStudyData(marketYearCategoryId: any, studyProjectMarketYearId: any) {
    this.isLoadingService.add();
    this.studyService
      .getStudyData(marketYearCategoryId, studyProjectMarketYearId)
      .subscribe(
        (res: DataSourceResponse) => {
          if (res.HttpStatusCode === 200) {
            this.studyData = [];
            this.studyData = res as DataSourceResponse;
            this.studySummary = this.studyData.Data.StudySetup;
            this.hasStudyData =
              this.studyData.Data.DataList !== null &&
                this.studyData.Data.DataList.length > 0
                ? true
                : false;
            this.setStudyCurrencyAndUnits(this.studyData.Data);
            this.marketYearCategoryId = this.studyData.Data.MarketYearCategoryId;
            this.isLoadingService.remove();
          } else {
            // this.alertService.danger(res.Data);
            this.errorOccured = true;
            this.alertService.danger(
              'Something went wrong. Please try after sometime.'
            );
            this.isLoadingService.remove();
          }
        },
        (err) => {
          // console.log(err);
          this.errorOccured = true;
          this.alertService.danger(
            'Something went wrong. Please try after sometime.'
          );
          this.isLoadingService.remove();
        }
      );
  }

  processDataSourceDataGrid(dataSourceDataGrid: any) {
    let studyDataPivotGridPreview: any[] = [];
    for (let dataSourceItem of dataSourceDataGrid) {
      for (let yearItem in dataSourceItem.YearsData) {
        const columns = dataSourceItem.ColumnsData;
        const year = { Year: yearItem };
        const values = dataSourceItem.YearsData[yearItem];
        studyDataPivotGridPreview.push({ ...columns, ...year, ...values });
      }
    }
    this.studyDataPivotGridPreview = studyDataPivotGridPreview;
  }

  // Call save api for saving data
  saveStudyData(data: any) {
    const dataSourceData = data.dataSourceData;
    const unitOfValues = data.unitOfValues;
    const saveDataSourceData = {
      marketYearCategoryId: this.studyData.Data.MarketYearCategoryId,
      studySetup: this.studySummary,
      unitOfValues: unitOfValues,
      yearsList: this.studyData.Data.YearsList,
      data: dataSourceData,
    };

    this.isLoadingService.add();
    this.studyService.saveStudyData(saveDataSourceData).subscribe(
      (res: ApiResponse) => {
        if (res.HttpStatusCode === 200) {
          this.alertService.success(res.Data);
          // this.dataGrid?.instance.refresh();
          this.loadCurrentStudy();
          this.isLoadingService.remove();
        } else {
          this.alertService.danger(res.Data);
          this.isLoadingService.remove();
        }
      },
      (err) => {
        console.log(err);
        this.alertService.danger(
          'Something went wrong. Please try after sometime.'
        );
        this.isLoadingService.remove();
      }
    );
  }

  // Call request for sign off data
  requestForSignOff(event: any) {
    this.isLoadingService.add();
    this.studyService
      .requestForSignOffData(this.studySummary)
      .subscribe((res: ApiResponse) => {
        if (res.HttpStatusCode === 200) {
          this.alertService.success(res.Data);
          this.isLoadingService.remove();
        } else {
          this.alertService.danger(res.Data);
          this.isLoadingService.remove();
        }
      });
  }

  // Call request for change data
  requestForChange(event: any) {
    this.isLoadingService.add();
    this.studyService
      .requestForChangeData(this.studySummary)
      .subscribe((res: ApiResponse) => {
        if (res.HttpStatusCode === 200) {
          this.alertService.success(res.Data);
          this.isLoadingService.remove();
        } else {
          this.alertService.danger(res.Data);
          this.isLoadingService.remove();
        }
      });
  }

  // Call sign off data
  singOffData(dataSourceData: any) {
    this.signOffData = {
      data: dataSourceData,
      yearsList: this.studyData.Data.YearsList,
      marketYearCategoryId: this.studyData.Data.MarketYearCategoryId,
      studySetup: this.studySummary,
      unitOfValues: this.studyData.Data.UnitOfValues,
    };

    this.signOffConfirmPopup = true;
  }

  onSignOffData() {
    const formData = this.signOffData;

    this.isLoadingService.add();
    this.studyService.signOffStudyData(formData).subscribe(
      (res: ApiResponse) => {
        if (res.HttpStatusCode === 200) {
          this.alertService.success(res.Data);
          // this.dataGrid?.instance.refresh();
          this.loadCurrentStudy();
          this.isLoadingService.remove();
        } else {
          this.alertService.danger(res.Data);
          this.isLoadingService.remove();
        }
        this.onSignOffConfirmHidden();
      },
      (err) => {
        console.log(err);
        this.alertService.danger(
          'Something went wrong. Please try after sometime.'
        );
        this.onSignOffConfirmHidden();
        this.isLoadingService.remove();
      }
    );
  }

  onSignOffConfirmHidden() {
    this.signOffData = {};
    this.signOffConfirmPopup = false;
  }

  unlockStudy() {
    this.disableStudyComponent?.unlockStudy();
  }

  // call when delete row button clicked
  onDeleteRow(row: any) {
    if (row) {
      this.deleteRowData = row;
      this.deleteRowConfirmPopup = true;
    }
  }

  onDeleteRowConfirmPopupHide() {
    this.deleteRowData = {};
    this.deleteRowConfirmPopup = false;
  }

  confirmDeleteRow() {
    this.HandsonTableComponent.onDeleteRow(this.deleteRowData);
    this.onDeleteRowConfirmPopupHide();
    // this.isLoadingService.add();
    // this.sharedService
    //   .removeStudyDataRow(
    //     this.studySummary.StudyDataSetId as number,
    //     this.studySummary.StudyAreaId as number,
    //     this.marketYearCategoryId,
    //     this.deleteRowData.DataRowId
    //   )
    //   .subscribe(
    //     (res: ApiResponse) => {
    //       this.onDeleteRowConfirmPopupHide();
    //       if (res.Data) {
    //         this.alertService.success(
    //           'Your data row has been removed successfully.'
    //         );
    //         this.loadCurrentStudy();
    //       } else {
    //         this.alertService.danger(
    //           'Error: Something went wrong. Data row can not removed.'
    //         );
    //       }
    //       this.isLoadingService.remove();
    //     },
    //     (err) => {
    //       const msg =
    //         typeof err.error !== 'undefined' && err.error !== ''
    //           ? err.error
    //           : 'Error: Something went wrong.';

    //       this.onDeleteRowConfirmPopupHide();
    //       this.alertService.danger(msg);
    //       this.isLoadingService.remove();
    //     }
    //   );
  }

  loadCurrentStudy() {
    let currentUrl = this.router.url;
    this.ngZone.run(() => {
      this.router.navigateByUrl('/', { skipLocationChange: true }).then(() => {
        this.router.navigate([currentUrl]);
      });
    });
  }

  // call for support data
  public getCurrencyList(studyDataSetId: number) {
    this.sharedService.getCurrencies(studyDataSetId).subscribe(
      (res: ApiResponse) => {
        if (res.HttpStatusCode === 200) {
          this.currencyDropdown = res.Data as DropdownCurrency[];
          this.currencyDropdown.map((item: any) => {
            item.CurrencyName = `${item.Name} (${item.CurrencyCode})`;
            this.setStudyCurrencyAndUnits(this.studyData);
          });
        }
      },
      (error: any) => {
        throw new Error(error);
      }
    );
  }

  public getUnitOfMassList(studyDataSetId: number) {
    this.sharedService.getVolumeUnits(studyDataSetId).subscribe(
      (res: ApiResponse) => {
        if (res.HttpStatusCode === 200) {
          this.massUnitDropdown = res.Data as DropdownUnits[];
          this.setStudyCurrencyAndUnits(this.studyData);
        }
      },
      (error: any) => {
        throw new Error(error);
      }
    );
  }

  public setStudyCurrencyAndUnits(data: any) {
    if (data?.UnitOfValues) {
      const volume = data.UnitOfValues.find(
        (f: { Title: string }) => f.Title === 'Volume'
      );
      if (volume) {
        this.currentVolumeUnit = this.massUnitDropdown.find(
          (f) => f.Id === volume.Id
        ) as DropdownUnits;
      }
      const currency = data.UnitOfValues.find(
        (f: { Title: string }) => f.Title === 'Currency'
      );
      if (currency) {
        this.currenCurrency = this.currencyDropdown.find(
          (f) => f.Id === currency.Id
        ) as DropdownCurrency;
      }
    }
  }
}
