import { Component, OnInit } from '@angular/core';
import { AlertService } from '@full-fledged/alerts';
import { IsLoadingService } from '@service-work/is-loading';
import { Common } from '../../extensions/common.extension';
import { ApiResponse } from '../../model/apiResponse.model';
import { DataGridConfiguration } from '../../model/dataGridConfiguration.model';
import { SettingsService } from '../../services/settings.service';

@Component({
  selector: 'app-data-grids-configuration',
  templateUrl: './data-grids-configuration.component.html',
  styleUrls: ['./data-grids-configuration.component.css']
})
export class DataGridsConfigurationComponent implements OnInit {
  studyDatasetAndCategories: any;
  studyDatasetAndCategoriesTree: any;
  parsingJsonSettingsErrorMessage: string = "";
  selectedDataItemCategory: any;
  currentlyLoadedSettingsJson: string = "";
  currentlyLoadedSettingsObj: any;

  constructor(
    private settingsService: SettingsService,
    private alertService: AlertService,
    private isLoadingService: IsLoadingService) { }

  ngOnInit(): void {
    this.getStudyDatasetAndCategories();
  }

  getStudyDatasetAndCategories() {
    this.isLoadingService.add();
    this.settingsService.getStudyDatasetAndCategories().subscribe(
      (data: ApiResponse) => {
        if (data?.HttpStatusCode === 200) {
          this.createStudyDatasetAndCategoriesTreeSource(data.Data);
          this.isLoadingService.remove();
        }
      }
    );
  }

  createStudyDatasetAndCategoriesTreeSource(studyDatasetAndCategories: any[]) {
    let id = 1;
    let studyDatasetAndCategoriesTree: any[] = [];
    for (let category of studyDatasetAndCategories) {
      let dataset = studyDatasetAndCategoriesTree.find(f => f.dataSetId === category.DataSetId);
      if (dataset) {
        dataset.items.push({
          id: id++,
          dataItemCategoryId: category.DataItemCategoryId,
          text: category.DataItemCategoryName,
          isRoot: false
        });
      }
      else {
        studyDatasetAndCategoriesTree.push({
          id: id++,
          dataSetId: category.DataSetId,
          text: category.DataSetName,
          isRoot: true,
          items: [
            {
              id: id++,
              dataItemCategoryId: category.DataItemCategoryId,
              text: category.DataItemCategoryName,
              isRoot: false
            }]
        });
      }
    }
    console.log(studyDatasetAndCategoriesTree);
    this.studyDatasetAndCategoriesTree = studyDatasetAndCategoriesTree;
  }

  onCategoryClick(e: any) {
    if (e.itemData.isRoot === false) {
      this.selectedDataItemCategory = e.itemData;
      this.getDataGridSettings(e.itemData.dataItemCategoryId);
    }
  }

  getDataGridSettings(dataItemCategoryId: number) {
    this.isLoadingService.add();
    this.settingsService.getDataGridSettings(dataItemCategoryId).subscribe(
      (data: ApiResponse) => {
        if (data?.HttpStatusCode === 200) {
          try {
            console.log(data.Data);
            const settingsObj = Common.realMerge(new DataGridConfiguration(), JSON.parse(data.Data[0]?.DataJson as string || "{}"));
            this.currentlyLoadedSettingsObj = settingsObj
            this.currentlyLoadedSettingsJson = JSON.stringify(settingsObj, null, 2);
          }
          catch (e) {
            this.parsingJsonSettingsErrorMessage = e as string;
          }
          finally {
            this.isLoadingService.remove();
          }
        } else {
          this.alertService.danger(data?.Data);
          this.isLoadingService.remove();
        }
      }
    );
  }

  saveDataGridSettings() {
    this.parsingJsonSettingsErrorMessage = "";
    let settingsJson = this.currentlyLoadedSettingsJson;
    try {
      //try to parse and check if JSON is valid and stringify it to make it minimized
      const parsedJson = JSON.parse(settingsJson);
      settingsJson = JSON.stringify(parsedJson);
    }
    catch (e) {
      console.log(e);
      this.parsingJsonSettingsErrorMessage = e as string;
    }

    if (this.parsingJsonSettingsErrorMessage.length === 0) {

      this.isLoadingService.add();
      this.settingsService.saveDataGridSettings(this.selectedDataItemCategory.dataItemCategoryId, settingsJson).subscribe(
        (data: ApiResponse) => {
          if (data?.HttpStatusCode === 200) {
            this.alertService.success(data?.Data)
            this.isLoadingService.remove();
          } else {
            this.alertService.danger(data?.Data);
            this.isLoadingService.remove();
          }
        }
      );
    }
  }
}
