import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { CalculatorService } from 'src/services/calculator.service';
import notify from 'devextreme/ui/notify';
import { SharedService } from 'src/services/shared.service';

@Component({
  selector: 'app-input-selector',
  templateUrl: './input-selector.component.html',
  styleUrls: ['./input-selector.component.css'],
})
export class InputSelectorComponent implements OnInit {
  calculatorInputsDataFull: any;
  calculatorInputsData: any;
  calculatorData: any;
  inputGroups: string[] = [];
  countOfPages: number = 0;
  pageNumber: number = 1;

  prewPageButtonAllowed: boolean = false;
  nextPageButtonAllowed: boolean = true;
  buttonCalculateAllowed: boolean = false;
  isDependentValue: boolean = false;
  isDataContainsRequired: boolean = false;

  selectedCalculator: number = 0;

  computedExcipients: any = [];

  supplierCountDpValues: number[] = [];

  suppliers: any[] = [];

  @Input() set calculatorId(id: any) {
    if (id) {
      this.selectedCalculator = id;
    }
  }

  @Input() set restart(canRestart: boolean) {
    if (canRestart) {
      this.restartInputComponent();
    }
  }

  Currency: any;
  @Input() set SetCurrency(item: any) {
    if (item) {
      this.Currency = item;
    }
  }

  Volume: any;
  @Input() set SetVolume(item: any) {
    if (item) {
      this.Volume = item;
    }
  }

  @Output() calculatedData = new EventEmitter<any>();
  @Output() calculatorTemplateReturnedData = new EventEmitter<any>();

  constructor(
    private calculatorService: CalculatorService,
    private sharedService: SharedService
  ) {}

  ngOnInit(): void {
    this.getAllInputsByCalculatorId();
    // this.getCalculatorDataById();
    this.getSupplierCountValues();
    this.getSuppliers();
  }

  getAllInputsByCalculatorId() {
    this.calculatorService
      .getAllInputsByCalculatorId(this.selectedCalculator)
      .subscribe(
        (data: any) => {
          if (data.HttpStatusCode === 200) {
            const items =
              typeof data.Data.CalculatorInputs !== 'undefined'
                ? data.Data.CalculatorInputs
                : [];
            this.calculatorInputsDataFull = items;
          } else {
            throw new Error('Something went wrong.');
          }
        },
        (error) => {
          throw new Error(error);
        },
        () => {
          this.calculatorInputsData = this.calculatorInputsDataFull.filter(
            (input: any) => input.PageNumber == 1
          );
          this.distinctGroups();
          this.canDisplayDependentInfo();
          this.dataContainsRequired();
          this.countOfPages = Math.max.apply(
            this,
            this.calculatorInputsDataFull.map((c: any) => c.PageNumber)
          );
        }
      );
  }

  getSuppliers() {
    this.sharedService.getSEDataEntryData('Suppliers').subscribe(
      (data: any) => (this.suppliers = data.Data),
      (error) => {
        throw new Error(error);
      }
    );
  }

  distinctGroups() {
    this.inputGroups = Array.from(
      new Set(this.calculatorInputsData.map((input: any) => input.GroupName))
    );
  }

  canDisplayDependentInfo() {
    this.isDependentValue =
      this.calculatorInputsData.filter(
        (input: any) =>
          input.IsDependentDefaultValue && input.DefaultValueEngineTypeId
      ).length > 0
        ? true
        : false;
  }

  dataContainsRequired() {
    this.isDataContainsRequired =
      this.calculatorInputsData.filter(
        (input: any) => input.IsRequired && input.IsVisible
      ).length > 0
        ? true
        : false;
  }

  getNoteText(): string {
    if (
      this.calculatorInputsData.filter(
        (input: any) => !input.IsDependentDefaultValue
      ).length > 0
    ) {
      return 'independent variables';
    } else if (
      this.calculatorInputsData.filter(
        (input: any) =>
          input.IsDependentDefaultValue && !input.DefaultValueEngineTypeId
      ).length > 0
    ) {
      return 'dependent variables';
    } else if (
      this.calculatorInputsData.filter(
        (input: any) =>
          input.IsDependentDefaultValue && input.DefaultValueEngineTypeId
      ).length > 0
    ) {
      return 'precalculated dependent variables';
    } else {
      return '';
    }
  }

  isGroupVisible(group: string): boolean {
    return this.calculatorInputsData.filter(
      (input: any) => input.IsVisible && input.GroupName == group
    ).length > 0
      ? true
      : false;
  }

  getStep(calculatorInput: any): number {
    if (calculatorInput.IsDecimalValue) {
      if (calculatorInput.DecimalScale == 1) {
        return 0.1;
      } else if (calculatorInput.DecimalScale == 2) {
        return 0.01;
      } else {
        return 0.001;
      }
    } else {
      return 1;
    }
  }

  goToPage(direction: number) {
    for (let input of this.calculatorInputsData) {
      input.Value = input.DefaultValue;
      input.ValueText = input.DefaultValueText;
    }

    if (direction == 1) {
      let filterInputsWithoutValues = this.calculatorInputsData.filter(
        (c: any) => c.IsRequired && !c.Value && !c.DefaultValueText
      );
      if (filterInputsWithoutValues.length > 0) {
        notify('*denotes a required value!', 'warning', 3000);
        return;
      }

      const isValidPer = this.validateCalculatorInputs();
      if (isValidPer !== true) {
        notify(isValidPer, 'error', 3000);
        return;
      }
    }

    let valuesForValidation = this.calculatorInputsData.filter(
      (inp: any) => inp.ValidatorEngineName && inp.ValidatorMethodName
    );
    if (direction == -1) {
      this.goToPageAction(direction);
    } else if (direction == 1 && valuesForValidation.length == 0) {
      this.goToPageAction(direction);
    } else {
      let validationResult: any;
      // this.calculatorService
      //   .validateValues(this.calculatorInputsDataFull, this.pageNumber)
      //   .subscribe(
      //     (data: any) => (validationResult = data),
      //     (error: any) => {
      //       throw new Error(error);
      //     },
      //     () => {
      //       if (
      //         validationResult &&
      //         validationResult.filter((res) => res.IsValid == false).length > 0
      //       ) {
      //         notify(
      //           validationResult.filter((res) => res.IsValid == false)[0]
      //             .Message,
      //           'error',
      //           3000
      //         );
      //         return;
      //       } else {
      //         this.goToPageAction(direction);
      //       }
      //     }
      //   );
      this.goToPageAction(direction);
    }
  }

  goToPageAction(direction: number) {
    // On last page call for data
    if (this.pageNumber == this.countOfPages && direction == 0) {
      const isValidPer = this.validateCalculatorInputs();
      if (isValidPer !== true) {
        notify(isValidPer, 'error', 3000);
        return;
      }

      this.callServiceForCalculatedData();
      return;
    }

    this.pageNumber = this.validatePageNumber(this.pageNumber, direction);
    if (this.pageNumber > 1) {
      this.getComputedDefaultValues(
        this.calculatorInputsDataFull,
        this.pageNumber
      );
    } else {
      this.calculatorInputsData = this.calculatorInputsDataFull.filter(
        (input: any) => input.PageNumber == 1
      );
      this.distinctGroups();
      this.canDisplayDependentInfo();
      this.dataContainsRequired();
      this.updateNavigationVisibility(this.pageNumber);
    }
    window.scrollTo(0, 0);
  }

  callServiceForCalculatedData() {
    let d: any;
    // const totalVolumeObj = this.calculatorInputsDataFull.find(
    //   (c: any) => c.GroupName === 'TotalVolume'
    // );
    // const totalVolume = totalVolumeObj.ValueText;
    const totalVolume = 0;

    // const totalSalesObj = this.calculatorInputsDataFull.find(
    //   (c: any) => c.GroupName === 'Sales'
    // );
    // const totalSales = totalSalesObj.ValueText;
    const totalSales = 0;

    this.calculatorService
      .getComponentDataByInputs(
        this.calculatorInputsDataFull,
        totalVolume,
        totalSales
      )
      .subscribe(
        (data: any) => (d = data.Data),
        (error) => {
          const msg =
            typeof error.error !== 'undefined'
              ? error.error
              : 'Invalid input value';
          // throw new Error(error);
          notify(msg, 'error', 3000);
        },
        () => {
          if (d) {
            this.calculatedData.emit(d);
          } else {
            this.restartInputComponent();
            notify('Data Not Found, restarting workflow...', 'error', 7000);
          }
        }
      );
  }

  validatePageNumber(currentPageNumber: number, direction: number): number {
    let pageNumber = currentPageNumber + direction;
    if (pageNumber >= 1 && pageNumber <= this.countOfPages) {
      return pageNumber;
    } else {
      return currentPageNumber;
    }
  }

  updateNavigationVisibility(pageNumber: number) {
    if (pageNumber == 1) {
      this.prewPageButtonAllowed = false;
      this.nextPageButtonAllowed = true;
    }
    if (pageNumber > 1 && pageNumber <= this.countOfPages) {
      this.prewPageButtonAllowed = true;
      this.nextPageButtonAllowed = true;
    }

    if (pageNumber == this.countOfPages) {
      this.prewPageButtonAllowed = true;
      this.nextPageButtonAllowed = false;
    }

    if (pageNumber == this.countOfPages) {
      this.buttonCalculateAllowed = true;
    } else {
      this.buttonCalculateAllowed = false;
    }
  }

  getSupplierCountValues() {
    for (let i = 1; i <= 15; i++) {
      this.supplierCountDpValues.push(i);
    }
  }

  getComputedExcipient() {
    return this.calculatorInputsDataFull.filter(
      (input: any) => input.GroupName == 'Excipient' && input.IsVisible
    );
  }

  getCalculatorDataById() {
    this.calculatorService
      .getCalculatorDataById(this.selectedCalculator)
      .subscribe(
        (data) => (this.calculatorData = data),
        (error) => {
          throw new Error(error);
        },
        () => {
          this.calculatorTemplateReturnedData.emit(this.calculatorData);
        }
      );
  }

  getComputedDefaultValues(calculatorInputsDataFull: any, pageNumber: number) {
    for (let input of calculatorInputsDataFull) {
      input.Value = input.DefaultValue;
    }

    this.calculatorService
      .getComputedDefaultValues(calculatorInputsDataFull, pageNumber)
      .subscribe(
        (data: any) => {
          if (data.HttpStatusCode === 200) {
            const items =
              typeof data.Data.CalculatorInputs !== 'undefined'
                ? data.Data.CalculatorInputs
                : [];
            this.calculatorInputsDataFull = items;
          } else {
            throw new Error('Something went wrong.');
          }
          // (this.calculatorInputsDataFull = data)
        },
        (error) => {
          throw new Error(error);
        },
        () => {
          if (this.calculatorInputsDataFull) {
            this.calculatorInputsData = this.calculatorInputsDataFull.filter(
              (input: any) => input.PageNumber == pageNumber
            );

            if (pageNumber === 4) {
              // console.log(this.calculatorInputsData);
              // this.calculatorInputsData.map(
              //   (item: any) => (item.IsVisible = true)
              // );
            }

            if (this.validateIfVisibleItemOrNot()) {
              this.distinctGroups();
              this.canDisplayDependentInfo();
              this.dataContainsRequired();
              this.updateNavigationVisibility(pageNumber);

              this.calculatorInputsData.map((item: any) => {
                if (item.IsDecimalValue) {
                  item.DxFormat = "#0.# '%'";
                }
              });

              // this.updateSuppliers();
            }
          } else {
            this.restartInputComponent();
            notify('Data Not Found, restarting workflow...', 'error', 7000);
          }
        }
      );
  }

  createRange(number: number) {
    // var items: number[] = [];
    // for(var i = 1; i <= number; i++){
    //   items.push(i);
    // }
    // return items;
    return new Array(number);
  }

  updateSuppliers() {
    this.inputGroups.map((group: any) => {
      if (group === 'Suppliers') {
        this.getComputedExcipient().map((item: any) => {
          if (item.SupplierCount > 0) {
            const suppliers = [];
            for (let i = 0; i < item.SupplierCount; i++) {
              suppliers.push({
                SupplierId: 0,
                SupplierName: null,
                Value: 0,
                Volume: 0,
                AvgPrice: 0,
              });
            }
            item.Suppliers = suppliers;
          }
        });
      }
    });
  }

  onChangeSupplier(
    e: any,
    index: number,
    calculatorInput: any,
    suppliers: any[]
  ) {
    const selectedSupplier = suppliers.find((item) => item.Id === e);
    calculatorInput.Suppliers[index].SupplierName = selectedSupplier.Name;
    // calculatorInput.Suppliers[index].SupplierId = e.Id;
    return calculatorInput;
  }

  validateCalculatorInputs() {
    let group = '';
    this.inputGroups.map((item: any) => {
      group = item;
    });

    if (this.calculatorInputsData.filter((c: any) => c.IsVisible).length == 0) {
      return true;
    }

    if (
      group !== 'Excipient' &&
      group !== 'SupplierCount' &&
      group !== 'Suppliers' &&
      group !== 'TotalVolume'
    ) {
      return true;
    }

    if (group === 'Excipient') {
      const totalPer = this.calculatorInputsData
        .filter(
          (c: any) => c.GroupName == group && c.IsVisible && c.Volume <= 0
        )
        .map((val: any) => val.DefaultValue)
        .reduce((sum: number, current: number) => sum + current, 0);

      const totalSelectedData = this.calculatorInputsData
        .filter((c: any) => c.GroupName == group && c.IsVisible)
        .map((val: any) => +val.Volume).length;

      const totalSelLength = this.calculatorInputsData
        .filter(
          (c: any) =>
            c.GroupName == group &&
            c.IsVisible &&
            c.Volume <= 0 &&
            c.DefaultValue <= 0
        )
        .map((val: any) => +val.Volume).length;

      return (totalSelLength != totalSelectedData && totalPer == 0) ||
        totalPer === 100
        ? true
        : 'You need to add excipient volume.';

      // : 'You need to set 100% for combinations.';
    } else if (group === 'SupplierCount') {
      const noSelectedCount = this.getComputedExcipient().filter(
        (c: any) => c.Value > 0 && c.SupplierCount === 0
      );

      return noSelectedCount.length === 0
        ? true
        : 'Please select supplier count.';
    } else if (group === 'Suppliers') {
      let supplierError = 0;
      let avgPriceError = 0;
      let totalPerError = 0;
      this.getComputedExcipient().map((val: any) => {
        const selectedSuppliers = [];

        for (let i = 0; i < val.SupplierCount; i++) {
          selectedSuppliers.push(val.Suppliers[i]);
        }

        const emptySuppliers = selectedSuppliers.filter(
          (v: any) => v.SupplierName === null || v.SupplierName === ''
        );

        const emptyAvgPrice = selectedSuppliers.filter(
          (v: any) =>
            v.AvgPrice === null || v.AvgPrice === '' || v.AvgPrice === 0
        );

        const totalPer = selectedSuppliers
          .map((v: any) => v.Value)
          .reduce((sum: number, current: number) => sum + current);

        const totalSelLength = selectedSuppliers
          .filter((c: any) => c.Value <= 0 && c.Volume <= 0)
          .map((val: any) => +val.Volume).length;

        if (emptySuppliers.length > 0) {
          supplierError++;
          // return 'Please select supplier.';
        }

        if ((totalSelLength == 0 && totalPer == 0) || totalPer == 100) {
          // return 'You need to set 100% for combinations of suppliers.';
        } else {
          totalPerError++;
        }

        if (emptyAvgPrice.length > 0) {
          avgPriceError++;
        }
      });

      if (supplierError > 0) {
        return 'Please select supplier.';
      } else if (totalPerError > 0) {
        return 'You need to set 100% for combinations of suppliers.';
      } else if (avgPriceError > 0) {
        return 'Please enter avg. price.';
      } else {
        return true;
      }
    } else if (group === 'TotalVolume') {
      const totalPer = this.getComputedExcipient()
        .map((val: any) => val.DefaultValue)
        .reduce((sum: number, current: number) => sum + current, 0);

      const totalVolume = this.getComputedExcipient()
        .map((val: any) => val.Volume)
        .reduce((sum: number, current: number) => sum + current, 0);

      const totalVolumeEnter = this.calculatorInputsData
        .filter((c: any) => c.GroupName == group && c.IsVisible)
        .map((val: any) => +val.ValueText)
        .reduce((sum: number, current: number) => sum + current, 0);

      if (totalPer === 0 && totalVolume !== totalVolumeEnter) {
        return `Total Volume should be same as sum of volumes of excipient's taken from page #3`;
      } else if (totalPer > 0 && totalVolume >= totalVolumeEnter) {
        return `Please enter volume greater than the total excipient volume.`;
      } else {
        return true;
      }
    } else {
      return true;
    }
  }

  restartInputComponent() {
    this.calculatorInputsDataFull = undefined;
    this.calculatorInputsData = undefined;
    this.calculatorData = undefined;
    this.inputGroups = [];
    this.countOfPages = 0;
    this.pageNumber = 1;

    this.prewPageButtonAllowed = false;
    this.nextPageButtonAllowed = true;
    this.buttonCalculateAllowed = false;
    this.isDependentValue = false;
    this.isDataContainsRequired = false;

    this.getAllInputsByCalculatorId();
    // this.getCalculatorDataById();
  }

  addNewSupplier(data: any, index: number, calculatorInput: any) {
    if (!data.text) {
      data.customItem = null;
      return;
    }

    const newItem = {
      Name: data.text,
      ID: 0,
    };

    data.customItem = newItem;
    calculatorInput.Suppliers[index].SupplierName = data.text;
    calculatorInput.Suppliers[index].SupplierId = 0;
    return calculatorInput;
  }

  validateIfVisibleItemOrNot() {
    const isVisibleItems = this.calculatorInputsData.filter(
      (item: any) => item.IsVisible
    );
    if (isVisibleItems.length === 0) {
      this.restartInputComponent();
      notify('Data Not Found, restarting workflow...', 'error', 7000);
      return false;
    }
    return true;
  }

  getGroupTitle(group: string) {
    if (group === 'Excipient' || group === 'TotalVolume') {
      return `${group} (${this.Volume.Name})`;
    }
    return group.replace(/([A-Z])/g, ' $1').trim();
  }

  setValueOrVolume(e: any, excipient: any, i: number, type: string) {
    const totalVolume = +excipient.Volume;
    const singleSupplier = excipient.Suppliers[i];
    const enterValue = +e.value;
    // const ValuePer = +singleSupplier.Value;
    // const Volume = +singleSupplier.Volume;

    if (type == 'value' && enterValue > 0) {
      const calVolume = ((totalVolume * enterValue) / 100).toFixed(1);
      excipient.Suppliers[i].Volume = +calVolume;
    }

    if (type == 'volume' && enterValue > 0) {
      const calPer = ((enterValue / totalVolume) * 100).toFixed(0);
      excipient.Suppliers[i].Value = +calPer;
    }
  }

  getCurrencyAndUnit() {
    const CurrencyCode = this.Currency.CurrencyCode || 'USD';
    const VolumeName = this.Volume.Name || 'Kg';

    return `${CurrencyCode}/${VolumeName}`;
  }

  onChangeSupplierCount(selectedObject: any, calculatorInput: any) {
    const currentValue = selectedObject.value;
    const previousValue = selectedObject.previousValue;

    if (currentValue !== previousValue) {
      calculatorInput.Suppliers.map((item: any) => {
        item['SupplierId'] = null;
        item['SupplierName'] = null;
        item['AvgPrice'] = 0;
        item['Value'] = 0;
        item['Volume'] = 0;
      });
    }
  }
}
