import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { ReactiveFormsModule } from '@angular/forms';

import { AppRoutingModule } from './app-routing.module';
import { MsalModule, MsalService, MSAL_INSTANCE } from '@azure/msal-angular';
import {
  IPublicClientApplication,
  PublicClientApplication,
} from '@azure/msal-browser';
import {
  HttpClientModule,
  HttpClient,
  HTTP_INTERCEPTORS,
} from '@angular/common/http';
import { AppComponent } from './app.component';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { InitialSetupComponent } from './initial-setup/initial-setup.component';
import { LocalStorageService } from 'src/services/localStorage.service';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { ModalModule } from 'ngx-bootstrap/modal';
import { DxDataGridModule } from 'devextreme-angular';

import { PagesModule } from './pages/pages.module';
import { SharedModule } from './shared/shared.module';
import { JwtInterceptor } from 'src/guard/jwt.interceptor';
import { Constants } from './Constant';
import { AuthGuard } from 'src/guard/auth.guard';
import { NgSelectModule } from '@ng-select/ng-select';
import { AlertModule } from '@full-fledged/alerts';
import { HashLocationStrategy, LocationStrategy } from '@angular/common';
import { ErrorInterceptor } from 'src/guard/error.interceptor';
import { LoginComponent } from './login/login.component';

export function MSALInstanceFactory(): IPublicClientApplication {
  return new PublicClientApplication({
    auth: {
      // clientId: '26ce7496-7c7e-4f8d-adef-e99f6a80a6c4',
      clientId: Constants.MSAL_CLIENT_ID,
      redirectUri: window.location.origin,
    },
  });
}

@NgModule({
  declarations: [AppComponent, InitialSetupComponent, LoginComponent],
  imports: [
    BrowserModule,
    ReactiveFormsModule,
    PagesModule,
    AppRoutingModule,
    NgbModule,
    HttpClientModule,
    MsalModule,
    BrowserAnimationsModule,
    ModalModule.forRoot(),
    NgSelectModule,
    SharedModule,
    AlertModule.forRoot({
      maxMessages: 1,
      timeout: 5000,
      positionX: 'right',
      positionY: 'top',
    }),
    DxDataGridModule,
  ],
  providers: [
    {
      provide: MSAL_INSTANCE,
      useFactory: MSALInstanceFactory,
    },
    MsalService,
    AuthGuard,
    LocalStorageService,
    { provide: HTTP_INTERCEPTORS, useClass: JwtInterceptor, multi: true },
    { provide: HTTP_INTERCEPTORS, useClass: ErrorInterceptor, multi: true },
    { provide: LocationStrategy, useClass: HashLocationStrategy },
  ],
  bootstrap: [AppComponent],
})
export class AppModule {}
